import { SET_DATEPICKER_INNER } from '../actions/datepickerActions';

const initialState = {
  datePickerInner: null,
};

const datepicker = (state = initialState, action) => {
  switch (action.type) {
    case SET_DATEPICKER_INNER:
      return {
        ...state,
        datePickerInner: action.data,
      };
    default:
      return state;
  }
};

export default datepicker;
