/**
 * LoadingScreen.js
 * Loading screen for anywhere use
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import Button from 'components/FormsV3/Button/Button';
import LogoSlider from './LogoSlider';
import SpinnerAnimation from '../SpinnerAnimation/SpinnerAnimation';
import Popup from '../Popup/Popup';

import { clearLoading } from '../../../actions/loadingScreenActions';
import './LoadingScreen.scss';

const timeOutTimer = 5000;

class LoadingScreen extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.isLoading !== prevState.isLoading) {
      return ({
        isLoading: nextProps.isLoading,
      });
    }

    return null;
  }


  constructor(props) {
    super(props);

    this.state = {
      isLoading: this.props.isLoading,
      zIndex: -1,
      loadingData: null,
    };

    this.handleTimeout = this.handleTimeout.bind(this);
    this.handleOnClick = this.handleOnClick.bind(this);
  }

  componentDidUpdate(prevProps, prevState) {
    const { isLoading } = this.state;
    const { loadingData } = this.props;

    if (isLoading !== prevState.isLoading) {
      if (isLoading) {
        this.setState({
          zIndex: 9999,
          loadingData,
        });

        this.popupRef.closePopup();
        clearTimeout(this.timeout);
        const time = loadingData && loadingData.time ? loadingData.time : timeOutTimer;
        this.timeout = setTimeout(this.handleTimeout, time);
        return;
      }

      setTimeout(() => this.setState({
        zIndex: -1,
      }), 400);
    }
  }

  handleTimeout() {
    const { isLoading } = this.state;

    if (isLoading) {
      this.popupRef.openPopup();
    }
  }

  handleOnClick() {
    const { loadingData } = this.state;
    const { loadingEnd } = this.props;
    this.popupRef.closePopup();
    loadingEnd();

    if (loadingData && loadingData.callBack) {
      loadingData.callBack();
      clearTimeout(this.timeout);
    }
  }

  render() {
    const {
      isLoading,
      zIndex,
      loadingData,
    } = this.state;

    const loadingStyle = isLoading ? 'loading-active' : 'loading-inactive';
    const mainMessage = loadingData && loadingData.mainMsg !== null ? loadingData.mainMsg : 'An error has occured!';
    const extraMessage = loadingData && loadingData.errorMsg !== null ? loadingData.errorMsg : 'Your session has expired. If the problem persists, please refresh your browser.';

    return (
      <Fragment>
        <div
          className={`loading-screen ${loadingStyle}`}
          style={{ zIndex }}
        >
          {
            isLoading ?
              <Fragment>
                <SpinnerAnimation />
                <div className="bottom-container">
                  <p>Searching from esteemed partners...</p>
                  <LogoSlider />
                </div>
              </Fragment>
            : null
          }
        </div>
        <Popup ref={(r) => { this.popupRef = r; }} hideCloseButton>
          <Fragment>
            <Row className="w-100" noGutters>
              <Col className="loading-error">
                <p className="error-text-main">{mainMessage}</p>
                <p className="error-text-extra">{extraMessage}</p>
                <p className="error-text-try">Please try again!</p>
                <Button
                  variant="medium-flat-green"
                  width={165}
                  height={72}
                  onClick={this.handleOnClick}
                >
                  <p>Continue</p>
                </Button>
              </Col>
            </Row>
          </Fragment>
        </Popup>
      </Fragment>
    );
  }
}

LoadingScreen.defaultProps = {
  loadingData: null,
};

LoadingScreen.propTypes = {
  isLoading: PropTypes.bool.isRequired,
  loadingData: PropTypes.object,
  loadingEnd: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  isLoading: state.loadingScreen.isLoading,
  loadingData: state.loadingScreen.loadingData,
});

const mapDispatchToProps = dispatch => ({
  loadingEnd: () => {
    dispatch(clearLoading());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(LoadingScreen);
