import Axios from '../lib/axiosFunctions';
import moment from 'moment';
import { URL_REWARD_FEED } from 'constants.js';

export const GET_BLOG_FEED_SUCCESS = 'GET_BLOG_FEED_SUCCESS';
export const GET_BLOG_FEED_FAIL = 'GET_BLOG_FEED_FAIL';
export const GET_BLOG = 'GET_BLOG';

const parseBlogData = (json) => {
  // let blogContentMain = null;
  const result = [];

  if (json) {
    for (let i = 0; i < json.length; ++i) {
      const item = json[i];

      const blogObject = {};
      let date = 'date_gmt' in item ? item.date_gmt : null;

      if (date) {
        date = date.split('T');
        blogObject.date = `PolicyPal - ${date[0]}`;
      }

      blogObject.link = 'link' in item ? item.link : null;
      blogObject.title =
        'title' in item && 'rendered' in item.title
          ? item.title.rendered
          : null;

      // if (i === 0) {
        let excerpt =
          'excerpt' in item && 'rendered' in item.excerpt
            ? item.excerpt.rendered
            : null;
        excerpt = excerpt.replace(/<\/?[^>]+(>|$)/g, '');

        if (excerpt) {
          const helix = excerpt.lastIndexOf('[&hellip;]');

          if (helix !== -1) {
            excerpt = `${excerpt.substring(0, helix)}...`;
          }

          excerpt = excerpt.replace(/&nbsp;/gi, '');

          blogObject.excerpt = excerpt;
        }
      // }

      if (
        '_embedded' in item &&
        'wp:featuredmedia' in item._embedded &&
        item._embedded['wp:featuredmedia'].length > 0 &&
        'media_details' in item._embedded['wp:featuredmedia'][0] &&
        'sizes' in item._embedded['wp:featuredmedia'][0].media_details
      ) {
        const sizes =
          item._embedded['wp:featuredmedia'][0].media_details.sizes;

        // Attempt to get medium large
        blogObject.img =
          'medium_large' in sizes && 'source_url' in sizes.medium_large
            ? sizes.medium_large.source_url
            : null;

        // If fail get the full one
        if (!blogObject.img) {
          blogObject.img =
            'full' in sizes && 'source_url' in sizes.full
              ? sizes.full.source_url
              : null;
        }

        // If still fail just get the largest height one
        if (!blogObject.img) {
          let height = 0;
          let img_link = null;
          for (const key in sizes) {
            if (sizes.hasOwnProperty(key)) {
              const val = sizes[key];
              if (
                'height' in val &&
                height < val.height &&
                'source_url' in val
              ) {
                height = val.height;
                img_link = val.source_url;
              }
            }
          }

          blogObject.img = img_link;
        }
      }

      // if (i === 0) {
      //   blogContentMain = blogObject;
      // } else {
        result.push(blogObject);
      // }
    }

    // return {
    //   blogContentMain,
    //   blogContentSub,
    // };
  }

  return result;
};

export const fetchBlogFeed = () => async (dispatch) => {
  await fetch('https://blog.policypal.com/wp-json/wp/v2/posts?per_page=4&_embed=true')
    .then(response => response.json())
    .then((json) => {
      const parsedData = parseBlogData(json);
      dispatch({
        type: GET_BLOG_FEED_SUCCESS,
        data: parsedData,
      });
    })
    .catch((err) => {
      // console.log(err);
      dispatch({
        type: GET_BLOG_FEED_FAIL,
        data: null,
      });
    });
};


const fetchBlog = (url = '') => async (dispatch) => {
  dispatch({
    type: `${GET_BLOG}_STARTED`,
  });

  try {
    if (url) {
      const response = await Axios.GET(url);

      if (response.status === 200) {
        const responseData = response.data;
        const result = [];
        
        for(let i = 0; i < responseData.length; i++) {
          const item = responseData[i];

          result.push({
            title: item.title.rendered,
            link: item.link,
            created_date: moment(item.date || 0).format('D MMM YYYY'),
            image: item._embedded['wp:featuredmedia'][0].source_url || '',
          });
        }
        
        dispatch({
          type: `${GET_BLOG}_SUCCESS`,
          payload: result || [],
        });
      } else {
        throw Error('Can\'t get data.');
      }
    } else {
      throw Error('URL is empty.');
    }
  } catch (error) {
    dispatch({
      type: `${GET_BLOG}_FAILURE`,
      payload: error.message || error,
    });
  }
};


export const fetchRewardBlog = () => {
  return fetchBlog(URL_REWARD_FEED);
}
