import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Helmet } from 'react-helmet';
import { withRouter } from 'react-router-dom';
import { Container } from 'reactstrap';
import Intercom from 'react-intercom';
import PropTypes from 'prop-types';
import withTracker from 'withTracker';
import { hotjar } from 'react-hotjar';


import NavigationContainer from '../containers/NavContainer';
import LoadingScreen from '../components/Templates/LoadingScreen/LoadingScreen';

// import Popup
import Popup from '../components/Templates/Popup/Popup';
import LoginContainer from '../containers/Signing/LoginContainer';

import Routes from './Routes';
import DateInner from '../components/Forms/DatePicker/DateInner';

import { setLoginPopup } from '../actions/loginActions';
import { getConstants } from '../actions/insuranceActions';
import { checkLoginStatus } from '../actions/userActions';
import { setDatepickerInner } from '../actions/datepickerActions';
import { isProduction } from '../lib/tagFunctions';

class App extends Component {
  constructor(props) {
    super(props);
    const { checkLogin, getConstantsData } = this.props;

    checkLogin();
    // getConstantsData();
  }

  componentDidMount() {
    const { doSetDatepickerInner } = this.props;
    this.props.setLoginPopup(this.popupRef, this.loginContainer);

    // window.fbq('init', process.env.REACT_APP_FB_TAG);
    // window.fbq('set', 'agent', 'tmgoogletagmanager', process.env.REACT_APP_FB_TAG);
    // window.fbq('track', 'PageView');

    doSetDatepickerInner(this.datepickerInner);

    // const hotjarSettings = isProduction ? { hjid: 1218971, hjsv: 6 } : { hjid: 1193604, hjsv: 6 };
    // hotjar.initialize(hotjarSettings.hjid, hotjarSettings.hjsv);

    // try {
    //   window.startApp();
    // } catch (error) {
    //   // eslint-disable-next-line
    // }
  }

  renderInner() {
    const { isInitalLoad } = this.props;

    // if (isInitalLoad) {
    //   return null;
    // }

    return (
      <Container fluid className="main-container-fluid">
        <Routes />
      </Container>
    );
  }

  render() {
    const meta = isProduction ? 'PolicyPal, insurance, insurance management, insurance app, compare insurance singapore, insurance broker, compare insurance, general insurance, insurance agent, online insurance singapore, insurance technology, insurance app, fintech, sandbox, MAS, insurtech' : '';

    // const hotJar = (
    //   <script>
    //     {
    //       // eslint-disable-next-line
    //       (function(h,o,t,j,a,r){
    //         // eslint-disable-next-line
    //         h.hj=h.hj||function(){(h.hj.q=h.hj.q||[]).push(arguments)};
    //         // eslint-disable-next-line
    //         h._hjSettings=hotjarSettings;
    //         // eslint-disable-next-line
    //         a=o.getElementsByTagName('head')[0];
    //         // eslint-disable-next-line
    //         r=o.createElement('script');r.async=1;
    //         // eslint-disable-next-line
    //         r.src=t+h._hjSettings.hjid+j+h._hjSettings.hjsv;
    //         a.appendChild(r);
    //         // eslint-disable-next-line
    //       })(window,document,'https://static.hotjar.com/c/hotjar-','.js?sv=')
    //     }
    //   </script>
    // );

    return (
      <div className="App">
        <Helmet>
          <meta name="keywords" content={meta} />
          <title>PolicyPal 2.0 - Insurance Made Easier</title>
          <meta name="title" content="PolicyPal 2.0 - Insurance Made Easier" />
          <meta name="description" content="PolicyPal is the insurance mobile app that helps you manage and get quality insurance policies at affordable prices." />
        </Helmet>
        <NavigationContainer />
        <DateInner ref={node => this.datepickerInner = node} />
        {this.renderInner()}
        <LoadingScreen />
        <Popup ref={(r) => { this.popupRef = r; }}>
          <LoginContainer ref={(r) => { this.loginContainer = r; }} />
        </Popup>
        {/* <Intercom appID="yltolfa1" /> */}
      </div>
    );
  }
}

App.defaultProps = {
};

App.propTypes = {
  checkLogin: PropTypes.func.isRequired,
  getConstantsData: PropTypes.func.isRequired,
  isInitalLoad: PropTypes.bool.isRequired,
  setLoginPopup: PropTypes.func.isRequired,
  doSetDatepickerInner: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  isInitalLoad: state.userDetails.isInitalLoad,
  login: state.login,
});

const mapDispatchToProps = dispatch => ({
  checkLogin: () => {
    dispatch(checkLoginStatus());
  },
  getConstantsData: () => {
    dispatch(getConstants());
  },
  setLoginPopup: (popup, loginContainer) => {
    dispatch(setLoginPopup(popup, loginContainer));
  },
  doSetDatepickerInner: (datepickerInner) => {
    dispatch(setDatepickerInner(datepickerInner));
  },
});

export default withRouter(withTracker(connect(mapStateToProps, mapDispatchToProps)(App)));

