import {
  GET_PET_PLANS,
  GET_PET_FIELDS_SUCCESS,
  CLEAR_PET_DATA,
  SET_PET_SELECTED_PLAN_DATA,
} from '../actions/petActions';

const initialState = {
  plansData: null,
  selectedPlanData: null,
  mainFormFields: null,
  petFields: null,
};

const pet = (state = initialState, action) => {
  switch (action.type) {
    case GET_PET_FIELDS_SUCCESS:
      return { ...state, petFields: action.data };
    case GET_PET_PLANS:
      return { ...state, plansData: action.data };
    case CLEAR_PET_DATA:
      return { ...initialState, petFields: state.petFields };
    case SET_PET_SELECTED_PLAN_DATA:
      return { ...state, selectedPlanData: action.data };
    default:
      return state;
  }
};

export default pet;
