// Amplitude tagging
const ampQueue = [];
let ampMutex = false;
let startTime = new Date();

// Loop function
const sendAmpTags = () => {
  if (amplitude) {
    // Check time passed is more than 10s
    const endTime = new Date();
    const isOverTime = (endTime - startTime) / 1000 > 10;

    // If more than 10s passed, just unlock mutex regardless and
    // reset timer
    if (isOverTime) {
      ampMutex = false;
      startTime = new Date();
    }

    // If still sending or empty queue return
    if (ampMutex || _.isEmpty(ampQueue)) {
      return;
    }

    // Restart the timer
    startTime = new Date();

    // Retrieve and delete from load
    const data = { ...ampQueue[0] };
    ampQueue.shift();

    // Lock it and wait for loop to send finish
    ampMutex = true;
    amplitude.track({
      event_type: data.eventName,
      event_properties: data.customData,
    }).then(function(result) {
      ampMutex = false;
    }).catch(function(error) {
      ampMutex = false;
    });
  }
};

// Amplitude tagging functions
