/**
 * GoToAppStore.js
 * Redirect to app store
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import { Component } from 'react';

class GoToAppStore extends Component {
  constructor(props) {
    super(props);

    this.DetectAndServe();
  }

  getMobileOperatingSystem() {
    const userAgent = navigator.userAgent || navigator.vendor || window.opera;

    if (/android/i.test(userAgent)) {
      return 'Android';
    }
    // iOS detection from: http://stackoverflow.com/a/9039885/177710
    if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
      return 'iOS';
    }
    if (/windows phone/i.test(userAgent)) {
      return 'Windows Phone';
    }
    return 'unknown';
  }

  DetectAndServe() {
    if (this.getMobileOperatingSystem() === 'Android') {
      window.location.href = 'https://play.google.com/store/apps/details?id=com.idvsgjo31w9m05f6raeb3&hl=en';
    } else {
      window.location.href = 'https://itunes.apple.com/sg/app/policypal/id1118511972?mt=8';
    }
  }

  render() {
    return null;
  }
}

export default GoToAppStore;
