export const etiqaMsigIndividualFields = [...individualFields, ...etiqaMsigAddress];

export const getIndividualFields = (selectedPlanData) => {
  const isEtiqa = selectedPlanData.info.insurer_slug === 'etiqa' || selectedPlanData.info.insurer_slug === 'tiq';
  const isMsig = selectedPlanData.info.insurer_slug === 'msig';

  const isBlk = isEtiqa || isMsig;
  return {
    isBlk,
    fields: isBlk ? etiqaMsigIndividualFields : normalIndividualFields,
  };
};

