export const getValuesToListen = values => [
  { value: 'trip_type', logo: PlaneLogo },
  {
    value: 'countries',
    logo: LocationLogo,
    customFunc: () => {
      try {
        if (values.countries.value.length === 1) {
          return values.countries.value[0].display_name;
        }
        return `${values.countries.value.length} Countries`;
      } catch (error) {
        console.log(error);
        return '';
      }
    },
  },
  {
    value: 'region',
    logo: LocationLogo,
  },
  {
    value: 'start_date',
    logo: DateLogo,
  },
  {
    value: 'end_date',
    logo: DateLogo,
    customFunc: () => {
      try {
        return getRangeString(values.start_date.value, values.end_date.value);
      } catch (error) {
        return '';
      }
    },
  }, {
    value: 'num_adult',
    logo: PeopleLogo,
    customFunc: () => {
      try {
        const sChildren = values.num_children.value > 1 ? 'Children' : 'Child';
        const children = values.num_children.value > 0 ? `, ${values.num_children.value} ${sChildren}` : '';

        const sAdult = values.num_adult.value > 1 ? 's' : '';
        return `${values.num_adult.value} Adult${sAdult}${children}`;
      } catch (error) {
        return '';
      }
    },
  },
];

