import Axios from '../lib/axiosFunctions';
import { storeStringToDateString } from '../lib/dateFunctions';

export const GET_LIFE_PLANS = 'GET_LIFE_PLANS';
export const GET_LIFE_PLANS_FAILED = 'GET_LIFE_PLANS_FAILED';
export const CLEAR_LIFE_DATA = 'CLEAR_LIFE_DATA';

export const GET_LIFE_FIELDS_SUCCESS = 'GET_LIFE_FIELDS_SUCCESS';
export const GET_LIFE_FIELDS_FAILED = 'GET_LIFE_FIELDS_FAILED';

export const GET_LIFE_VALID_INSURERS_LIST = 'GET_LIFE_VALID_INSURERS_LIST';
export const GET_LIFE_VALID_INSURERS_LIST_FAILED = 'GET_LIFE_VALID_INSURERS_LIST_FAILED';

export const SET_LIFE_SELECTED_PLAN_DATA = 'SET_LIFE_SELECTED_PLAN_DATA';

export const parseLifeForm = (lifeForm, insurers = null) => {
  const isTermLife = lifeForm.coverage_type === 'term_life';
  const insurersToUse = insurers ? { insurers } : {};

  const commonParams = {
    ...insurersToUse,
    gender: lifeForm.gender,
    smoker: lifeForm.smoker,
    birth_date: storeStringToDateString(lifeForm.birth_date),
    coverage_type: lifeForm.coverage_type,
  };

  if (isTermLife) {
    return {
      ...commonParams,
      sum_insured: lifeForm.sum_insured,
      critical_illness: lifeForm.critical_illness,
      premium_term: lifeForm.premium_term,
    };
  }

  return {
    ...commonParams,
    sum_insured: lifeForm.sum_insured_selection,
    premium_term: lifeForm.premium_whole,
  };
};

export const getLifeFields = () => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/4/formfields`;

  const result = await Axios.GET(url);

  if (
    !result ||
    result.status !== 200
  ) {
    return dispatch({
      type: GET_LIFE_FIELDS_FAILED,
      data: 'Life Fields data invalid',
    });
  }

  // console.log(result);
  return dispatch({
    type: GET_LIFE_FIELDS_SUCCESS,
    data: result.data,
  });
};

export const clearLifeData = () => (dispatch) => {
  dispatch({
    type: CLEAR_LIFE_DATA,
  });
};

export const getLifePlans = params => async (dispatch) => {
  const id = params.coverage_type === 'term_life' ? 4 : 3;
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/${id}/compare`;

  const result = await Axios.POST(url, params);

  // console.log(result);
  if (
    !result ||
    result.status !== 201
  ) {
    return dispatch({
      type: GET_LIFE_PLANS_FAILED,
      data: 'Get life plans failed',
    });
  }

  return dispatch({
    type: GET_LIFE_PLANS,
    data: result.data.insurances,
  });
};

export const getValidInsurersList = params => async (dispatch) => {
  const id = params.coverage_type === 'term_life' ? 4 : 3;
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/${id}/insurers`;

  const result = await Axios.POST(url, params);

  // console.log(result);
  if (
    !result ||
    result.status !== 201
  ) {
    return dispatch({
      type: GET_LIFE_VALID_INSURERS_LIST_FAILED,
      data: 'Get Life insurer list failed',
    });
  }

  return dispatch({
    type: GET_LIFE_VALID_INSURERS_LIST,
    data: result.data.insurances,
  });
};

export const setSelectedPlanData = data => (dispatch) => {
  dispatch({
    type: SET_LIFE_SELECTED_PLAN_DATA,
    data,
  });
};

export const getSessionParams = (lifeForm, lifeDetailForm, selectedPlanData) => {
  const category = lifeForm.coverage_type === 'term_life' ? 'Term Life' : 'Whole Life';

  return {
    sessionParams: {
      email: lifeDetailForm.email,
      client_name: selectedPlanData.info.insurer_slug,
      policy_name: selectedPlanData.info.product_name,
      client_display_name: selectedPlanData.info.insurer_name,
      agree_on_sharing: true,
      session_params: {
        ...parseLifeForm(lifeForm),
        ph_birth_date: storeStringToDateString(lifeForm.birth_date),
        email: lifeDetailForm.email,
        name: lifeDetailForm.name,
        pb_birth_date: storeStringToDateString(lifeDetailForm.birth_date),
        mobile_number: lifeDetailForm.mobile_number,
        category,
      },
    },
  };
};

export const postToRedirect = async (storeForm, storeDetailForm, selectedPlanData) => {
  let result = null;
  try {
    const params = getSessionParams(storeForm, storeDetailForm, selectedPlanData);
    const url = `${process.env.REACT_APP_API_HOST}/v2/anonymous_sessions/webview`;

    result = await Axios.POST(url, params.sessionParams);
    // console.log(result);

    if (result.status === 200 && result.data && result.data.url) {
      return result.data;
    }
  } catch (error) {
    console.log('Post failed');
  }

  return null;
};
