import {
  GET_PA_PLANS,
  GET_PA_FIELDS_SUCCESS,
  CLEAR_PA_DATA,
  SET_PA_SELECTED_PLAN_DATA,
  GET_PA_VALID_INSURERS_LIST,
} from '../actions/paActions';

const initialState = {
  plansData: null,
  selectedPlanData: null,
  validInsurersList: null,
  paFields: null,
};

const pa = (state = initialState, action) => {
  switch (action.type) {
    case GET_PA_FIELDS_SUCCESS:
      return { ...state, paFields: action.data };
    case GET_PA_PLANS:
      return { ...state, plansData: action.data };
    case CLEAR_PA_DATA:
      return { ...initialState, paFields: state.paFields };
    case SET_PA_SELECTED_PLAN_DATA:
      return { ...state, selectedPlanData: action.data };
    case GET_PA_VALID_INSURERS_LIST:
      return { ...state, validInsurersList: action.data };
    default:
      return state;
  }
};

export default pa;
