import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Loadable from 'react-loadable';

// const Navigation = Loadable({
//   loader: () => import(/* webpackChunkName: "navigation" */ '../containers/NavContainer'),
//   loading: () => null,
//   modules: ['navigation']
// });

const About = Loadable({
  loader: () => import(/* webpackChunkName: "about" */ './About/About'),
  loading: () => null,
  modules: ['about']
});

const Landing = Loadable({
  loader: () => import(/* webpackChunkName: "landing" */ '../containers/Landing/LandingContainer'),
  loading: () => null,
  modules: ['landing']
});

const Categories = Loadable({
  loader: () => import(/* webpackChunkName: "categories" */ './Categories/Categories'),
  loading: () => null,
  modules: ['categories']
});

const Career = Loadable({
  loader: () => import(/* webpackChunkName: "career" */ './Career/Career'),
  loading: () => null,
  modules: ['career']
});

const Cashback = Loadable({
  loader: () => import(/* webpackChunkName: "cashback" */ './Cashback/Cashback'),
  loading: () => null,
  modules: ['cashback']
});

const Faq = Loadable({
  loader: () => import(/* webpackChunkName: "faq" */ './Faq/Faq'),
  loading: () => null,
  modules: ['faq']
});

const Disclaimer = Loadable({
  loader: () => import(/* webpackChunkName: "disclaimer" */ './Disclaimer/Disclaimer'),
  loading: () => null,
  modules: ['faq']
});

const Tos = Loadable({
  loader: () => import(/* webpackChunkName: "tos" */ './Tos/Tos'),
  loading: () => null,
  modules: ['tos']
});

const PrivacyPolicy = Loadable({
  loader: () => import(/* webpackChunkName: "privacypolicy" */ './PrivacyPolicy/PrivacyPolicy'),
  loading: () => null,
  modules: ['privacypolicy']
});

const FairDealingStatement = Loadable({
  loader: () => import(/* webpackChunkName: "fairdealingstatement" */ './FairDealingStatement/FairDealingStatement'),
  loading: () => null,
  modules: ['fairdealingstatement']
});

const Security = Loadable({
  loader: () => import(/* webpackChunkName: "security" */ './Security/Security'),
  loading: () => null,
  modules: ['security']
});

const Press = Loadable({
  loader: () => import(/* webpackChunkName: "press" */ './Press/Press'),
  loading: () => null,
  modules: ['press']
});

const RewardTos = Loadable({
  loader: () => import(/* webpackChunkName: "rewardtos" */ './RewardTos/RewardTos'),
  loading: () => null,
  modules: ['rewardtos']
});

const Motor = Loadable({
  loader: () => import(/* webpackChunkName: "motor" */ '../containers/Motor/MotorRouteContainer'),
  loading: () => null,
  modules: ['motor']
});

const Pet = Loadable({
  loader: () => import(/* webpackChunkName: "pet" */ '../containers/Pet/PetRouteContainer'),
  loading: () => null,
  modules: ['pet']
});

const Travel = Loadable({
  loader: () => import(/* webpackChunkName: "travel" */ '../containers/Travel/TravelRouteContainer'),
  loading: () => null,
  modules: ['travel']
});

const PA = Loadable({
  loader: () => import(/* webpackChunkName: "pa" */ '../containers/PA/PARouteContainer'),
  loading: () => null,
  modules: ['pa']
});

const Retirement = Loadable({
  loader: () => import(/* webpackChunkName: "retirement" */ '../containers/Retirement/RetirementRouteContainer'),
  loading: () => null,
  modules: ['retirement']
});

const Life = Loadable({
  loader: () => import(/* webpackChunkName: "life" */ '../containers/Life/LifeRouteContainer'),
  loading: () => null,
  modules: ['life']
});

const Endowment = Loadable({
  loader: () => import(/* webpackChunkName: "endowment" */ '../containers/Endowment/EndowmentRouteContainer'),
  loading: () => null,
  modules: ['endowment']
});

const Business = Loadable({
  loader: () => import(/* webpackChunkName: "business" */ '../containers/Business/BusinessRouteContainer'),
  loading: () => null,
  modules: ['business']
});

const Rewards = Loadable({
  loader: () => import(/* webpackChunkName: "rewards" */ '../containers/Rewards/RewardsContainer'),
  loading: () => null,
  modules: ['rewards']
});

const Partnership = Loadable({
  loader: () => import(/* webpackChunkName: "partnership" */ '../containers/Partnership/PartnershipContainer'),
  loading: () => null,
  modules: ['partnership']
});

const Moovaz = Loadable({
  loader: () => import(/* webpackChunkName: "moovaz" */ '../containers/Moovaz/MoovazContainer'),
  loading: () => null,
  modules: ['moovaz']
});

const Referral = Loadable({
  loader: () => import(/* webpackChunkName: "referral" */ '../containers/Referral/ReferralContainer'),
  loading: () => null,
  modules: ['referral']
});

const Profile = Loadable({
  loader: () => import(/* webpackChunkName: "profile" */ '../containers/Profile/ProfileContainer'),
  loading: () => null,
  modules: ['profile']
});

const Claims = Loadable({
  loader: () => import(/* webpackChunkName: "claims" */ '../containers/Claims/ClaimsContainer'),
  loading: () => null,
  modules: ['claims']
});

const ForgetPassword = Loadable({
  loader: () => import(/* webpackChunkName: "forgetpassword" */ '../containers/ForgetPassword/ForgetPasswordContainer'),
  loading: () => null,
  modules: ['forgetpassword']
});

const AdHocCheckout = Loadable({
  loader: () => import(/* webpackChunkName: "adhoccheckout" */ '../containers/AdHocCheckout/AdHocCheckoutContainer'),
  loading: () => null,
  modules: ['adhoccheckout']
});

const ErrorPage = Loadable({
  loader: () => import(/* webpackChunkName: "errorpage" */ '../components/Templates/ErrorPage/ErrorPage'),
  loading: () => null,
  modules: ['errorpage']
});

export default () => (
  <Switch>
    <Route exact path="/" component={Landing} />
      <Route exact path="/about" component={About} />
      <Route exact path="/categories" component={Categories} />
      <Route exact path="/career" component={Career} />
      <Route exact path="/faq" component={Faq} />
      <Route exact path="/cashback" component={Cashback} />
      <Route exact path="/referral" component={Referral} />
      {/* <Route exact path="/marketplace" component={MarketPlaceInfo} /> */}
      <Route exact path="/tos" component={Tos} />
      <Route exact path="/mobile/tos" component={Tos} />
      <Route exact path="/disclaimer" component={Disclaimer} />
      <Route exact path="/mobile/disclaimer" component={Disclaimer} />
      <Route exact path="/privacypolicy" component={PrivacyPolicy} />
      <Route exact path="/mobile/privacypolicy" component={PrivacyPolicy} />
      <Route exact path="/mobile/reward-tos" component={RewardTos} />
      <Route exact path="/fair-dealing-statement" component={FairDealingStatement} />
      <Route exact path="/security" component={Security} />
      <Route exact path="/press" component={Press} />
      <Route exact path="/motor" component={Motor} />
      <Route exact path="/motor/:page" component={Motor} />
      <Route exact path="/pet" component={Pet} />
      <Route exact path="/pet/:page" component={Pet} />
      <Route exact path="/travel" component={Travel} />
      <Route exact path="/travel/:page/:insurer/:product" component={Travel} />
      <Route exact path="/travel/:page" component={Travel} />
      <Route exact path="/personal-accident" component={PA} />
      <Route exact path="/personal-accident/:page" component={PA} />
      <Route exact path="/life" component={Life} />
      <Route exact path="/life/:page" component={Life} />
      <Route exact path="/retirement" component={Retirement} />
      <Route exact path="/retirement/:page" component={Retirement} />
      <Route exact path="/endowment" component={Endowment} />
      <Route exact path="/endowment/:page" component={Endowment} />
      <Route exact path="/business" component={Business} />
      <Route exact path="/business/:page" component={Business} />
      <Route exact path="/claims" component={Claims} />
      <Route exact path="/profile" component={Profile} />
      <Route exact path="/rewards" component={Rewards} />
      <Route exact path="/partnership" component={Partnership} />
      <Route exact path="/moovaz" component={Moovaz} />
      {/* <Route exact path="/campaigns/cny-spin/:id" component={Spinner} />
      <Route exact path="/campaigns/cny-spin" component={Cny} /> */}
      <Route path="/forgotpassword" component={ForgetPassword} />
      <Route exact path="/receipt/:id" component={AdHocCheckout} />
      <Route exact path="/checkout/:id" component={AdHocCheckout} />
      <Route component={ErrorPage} />
  </Switch>
);
