export const signInGoogle = (response, onFail, onSuccess) => async (dispatch) => {
  // window.auth2.signIn()
  // .then(async (googleUser) => {
  const userEmail = response.getBasicProfile().getEmail();
  const accessToken = response.getAuthResponse(true).access_token;

  const params = {
    logInType: 'GOOGLE',
    logInToken: accessToken,
    email: userEmail,
  };

  const result = await login(dispatch, params, loginEnd);
  handleResult(result, 'GOOGLE', onFail, onSuccess);
  // })
};

export const signInEmailPassword = (params, onFail, onSuccess) => async (dispatch) => {
  const result = await login(dispatch, params, loginEnd);
  handleResult(result, 'DEFAULT', onFail, onSuccess);
};

export const forgetPassword = email => async (dispatch) => {
  if (!validateFormValue('email', email).result) {
    return dispatch({
      type: RESET_EMAIL_ERROR,
      data: 'Email not valid!',
    });
  }

  const url = `${process.env.REACT_APP_API_HOST}/user/forgetPassword`;

  const result = await Axios.POST(url, { email });

  if (result.status !== 200) {
    let errorString = '';

    try {
      if (result.data.error === 'not_found') {
        errorString = 'Email not registered!';
      }
    } catch (e) {
      errorString = 'Email reset failed. Please try again.';
    }
    return dispatch({
      type: RESET_EMAIL_ERROR,
      data: errorString,
    });
  }

  return dispatch({
    type: RESET_EMAIL_SUCCESS,
    data: 'An email has been sent to reset your password.',
  });
};

export const resetPassword = async (password, token) => {
  const url = `${process.env.REACT_APP_API_HOST}/user/recoverPassword`;

  const result = await Axios.POST(url, {
    password,
    token,
  });

  return result && result.data && result.data.updated;
};

export const signUp = params => async (dispatch) => {
  const {
    email,
    password,
    password2,
  } = params;

  let errString = null;
  if (!validateFormValue('email', email).result) {
    errString = 'Email not valid!';
  } else if (!password && !password2) {
    errString = 'No password entered!';
  } else if (password !== password2) {
    errString = 'Password mismatch! please try again.';
  } else if (password.length < 8) {
    errString = 'Password is too short (Minimum. 8 characters)';
  }

  if (errString) {
    return dispatch({
      type: SIGNUP_ERROR,
      data: errString,
    });
  }

  const url = `${process.env.REACT_APP_API_HOST}/user/`;
  const paramsToPost = {
    email,
    password,
  };

  const headers = {
    'Content-Type': 'application/json',
  };

  const result = await Axios.POST(url, paramsToPost, headers);

  // console.log(result);

  if (result.status !== 201) {
    let errorString = '';

    try {
      if (result.data.message === 'email is taken') {
        errorString = 'Email is taken';
      }
    } catch (e) {
      errorString = genericErrorString;
    }
    return dispatch({
      type: SIGNUP_ERROR,
      data: errorString,
    });
  }

  return dispatch({
    type: SIGNUP_SUCCESS,
    data: result.data,
  });
};

export const getRewardDetails = token => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/user/rewards`;
  const headers = {
    PolicyPalAuthorization: `PolicyPalBearer ${token}`,
  };

  const result = await Axios.GET(url, headers);

  // console.log(result);
  if (result.status !== 200 || (result.data.constructor === Array && result.data.length === 0)) {
    return dispatch({
      type: GET_REWARDS_ERROR,
    });
  }


  return dispatch({
    type: GET_REWARDS_SUCCESS,
    data: result.data,
  });
};

export const saveProfile = (token, profileForm) => async (dispatch) => {
  const params = { ...profileForm };
  params.dateOfBirth = formDateToISOString(params.dateOfBirth);
  const url = `${process.env.REACT_APP_API_HOST}/api/user/`;
  const headers = {
    PolicyPalAuthorization: `PolicyPalBearer ${token}`,
    'Content-Type': 'application/json',
  };

  const result = await Axios.PUT(url, params, headers);

  // console.log(result);
  if (!result || result.status !== 200 || !result.data || !result.data.updated) {
    dispatch({
      type: SAVE_PROFILE_FAILED,
    });

    return false;
  }

  dispatch({
    type: SAVE_PROFILE_SUCCESS,
    data: profileForm,
  });

  return true;
};

export const initLoading = () => async (dispatch) => {
  dispatch({
    type: USER_ACTION_LOADING,
  });
};
