/**
 * editFormActions.js
 * This controls the edit form component in compare view
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
export const INVOKE_REVERT = 'INVOKE_REVERT';
export const RESET_REVERT_STATUS = 'RESET_REVERT_STATUS';
export const EDIT_SUBMITTED = 'EDIT_SUBMITTED';
export const RESET_EDIT_SUBMITTED = 'RESET_EDIT_SUBMITTED';

export const invokeSubmit = () => (dispatch) => {
  dispatch({
    type: EDIT_SUBMITTED,
  });
};

export const resetSubmitStatus = () => (dispatch) => {
  dispatch({
    type: RESET_EDIT_SUBMITTED,
  });
};
