import React, { Component } from 'react';
import './Intercom.scss';

class Intercom extends Component {
  constructor(props) {
    super(props);

    window.intercomSettings = ('boot', {
      app_id: 'yltolfa1',
      hide_default_launcher: true,
    });
  }

  componentDidMount() {
    let stopLoading = false;
    // Wait for Intercom to boot (max 30 seconds)
    const timeout = setTimeout(() => stopLoading = true, 30000);
    const interval = setInterval(() => {
      const elem = document.getElementById('intercom-container');
      const elem2 = document.getElementById('blocker');

      // Intercom is booted!
      if (window.Intercom.booted && elem && elem2) {
        // Summon full view and show the messages
        window.Intercom('showMessages');

        // Reduce zdepth by abit so that blocker can block it
        elem.style.zIndex = '2147483646';

        // Display the blocker
        elem2.style.display = 'block';

        // Default have margin of 8, so disable it
        document.body.style.margin = '0';
        document.body.style.backgroundColor = '#00d7af';

        clearInterval(interval);
        clearTimeout(timeout);
      }

      if (stopLoading) {
        clearInterval(interval);
        clearTimeout(timeout);
      }
    }, 100);
  }

  render() {
    return <div id="blocker" className="blockerStyle" />;
  }
}

export default Intercom;
