import {
  USER_ACTION_LOADING,
  LOGOUT,
  SIGN_IN_SUCCESS,
  SIGN_IN_FAILED,
  SIGN_IN_FACEBOOK_ERROR,
  SIGN_IN_FACEBOOK_SUCCESS,
  SIGN_IN_GOOGLE_ERROR,
  SIGN_IN_GOOGLE_SUCCESS,
  SIGN_IN_DEFAULT_ERROR,
  SIGN_IN_DEFAULT_SUCCESS,
  GET_REWARDS_ERROR,
  GET_REWARDS_SUCCESS,
  RESET_EMAIL_ERROR,
  RESET_EMAIL_SUCCESS,
  SIGNUP_ERROR,
  SIGNUP_SUCCESS,
  SAVE_PROFILE_SUCCESS,
  CLEAR_USER_DATA,
} from '../actions/userActions';

const initialState = {
  isInitalLoad: true,
  isLoading: null,
  fbErrorMsg: null,
  googleErrorMsg: null,
  defaultErrorMsg: null,
  resetEmailErrorMsg: null,
  signUpErrorMsg: null,
  data: null,
  rewardData: null,
};

const userDetails = (state = initialState, action) => {
  switch (action.type) {
    case USER_ACTION_LOADING:
      return {
        ...state,
        isLoading: true,
      };
    case SIGN_IN_FACEBOOK_ERROR:
      return {
        ...state,
        fbErrorMsg: action.data,
        isLoading: false,
      };
    case LOGOUT:
      return {
        ...initialState,
        isInitalLoad: state.isInitalLoad,
      };
    case SIGN_IN_GOOGLE_ERROR:
      return {
        ...state,
        googleErrorMsg: action.data,
        isLoading: false,
      };
    case SIGN_IN_DEFAULT_ERROR:
      return {
        ...state,
        defaultErrorMsg: action.data,
        isLoading: false,
      };
    case RESET_EMAIL_SUCCESS:
    case RESET_EMAIL_ERROR:
      return {
        ...state,
        resetEmailErrorMsg: action.data,
        isLoading: false,
      };
    case SIGNUP_ERROR:
      return {
        ...state,
        signUpErrorMsg: action.data,
        isLoading: false,
      };
    case SIGN_IN_SUCCESS:
    case SIGN_IN_FACEBOOK_SUCCESS:
    case SIGN_IN_GOOGLE_SUCCESS:
    case SIGN_IN_DEFAULT_SUCCESS:
      return {
        ...state,
        isInitalLoad: false,
        googleErrorMsg: null,
        fbErrorMsg: null,
        defaultErrorMsg: null,
        resetEmailErrorMsg: null,
        signUpErrorMsg: null,
        data: action.data,
        isLoading: false,
      };
    case SIGNUP_SUCCESS:
      return {
        ...state,
        isInitalLoad: false,
        googleErrorMsg: null,
        fbErrorMsg: null,
        defaultErrorMsg: null,
        resetEmailErrorMsg: null,
        signUpErrorMsg: null,
        data: state.data,
        isLoading: false,
      };
    case SIGN_IN_FAILED:
      return {
        ...state,
        isInitalLoad: false,
        googleErrorMsg: null,
        fbErrorMsg: null,
        defaultErrorMsg: null,
        resetEmailErrorMsg: null,
        signUpErrorMsg: null,
        data: null,
        isLoading: false,
      };
    case GET_REWARDS_SUCCESS:
      return {
        ...state,
        rewardData: action.data,
        isLoading: false,
      };
    case GET_REWARDS_ERROR:
      return {
        ...state,
        rewardData: null,
        isLoading: false,
      };
    case SAVE_PROFILE_SUCCESS:
      return {
        ...state,
        data: {
          ...state.data,
          ...action.data,
        },
      };
    case CLEAR_USER_DATA:
      return {
        ...state,
        data: null,
        rewardData: null,
      };
    default:
      return state;
  }
};

export default userDetails;
