import Axios from '../lib/axiosFunctions';
import { storeStringToDateString } from '../lib/dateFunctions';

export const GET_RETIREMENT_PLANS = 'GET_RETIREMENT_PLANS';
export const GET_RETIREMENT_PLANS_FAILED = 'GET_RETIREMENT_PLANS_FAILED';
export const CLEAR_RETIREMENT_DATA = 'CLEAR_RETIREMENT_DATA';

export const GET_RETIREMENT_FIELDS_SUCCESS = 'GET_RETIREMENT_FIELDS_SUCCESS';
export const GET_RETIREMENT_FIELDS_FAILED = 'GET_RETIREMENT_FIELDS_FAILED';

export const GET_RETIREMENT_VALID_INSURERS_LIST = 'GET_RETIREMENT_VALID_INSURERS_LIST';
export const GET_RETIREMENT_VALID_INSURERS_LIST_FAILED = 'GET_RETIREMENT_VALID_INSURERS_LIST_FAILED';

export const SET_RETIREMENT_SELECTED_PLAN_DATA = 'SET_RETIREMENT_SELECTED_PLAN_DATA';

export const parseRetirementForm = (retirementForm, insurers = null) => {
  const insurersToUse = insurers ? { insurers } : {};
  return {
    ...insurersToUse,
    ...retirementForm,
    birth_date: storeStringToDateString(retirementForm.birth_date),
  };
};

export const getRetirementFields = () => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/6/formfields`;

  const result = await Axios.GET(url);

  if (
    !result ||
    result.status !== 200
  ) {
    return dispatch({
      type: GET_RETIREMENT_FIELDS_FAILED,
      data: 'Retirement Fields data invalid',
    });
  }

  // console.log(result);

  return dispatch({
    type: GET_RETIREMENT_FIELDS_SUCCESS,
    data: result.data,
  });
};

export const clearRetirementData = () => (dispatch) => {
  dispatch({
    type: CLEAR_RETIREMENT_DATA,
  });
};

export const getRetirementPlans = params => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/6/compare`;

  const result = await Axios.POST(url, params);

  // console.log(result);
  if (
    !result ||
    result.status !== 201
  ) {
    return dispatch({
      type: GET_RETIREMENT_PLANS_FAILED,
      data: 'Get pet plans failed',
    });
  }

  return dispatch({
    type: GET_RETIREMENT_PLANS,
    data: result.data.insurances,
  });
};

export const getValidInsurersList = params => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/6/insurers`;

  const result = await Axios.POST(url, params);

  // console.log(result);
  if (
    !result ||
    result.status !== 201
  ) {
    return dispatch({
      type: GET_RETIREMENT_VALID_INSURERS_LIST_FAILED,
      data: 'Get Retirement insurer list failed',
    });
  }

  return dispatch({
    type: GET_RETIREMENT_VALID_INSURERS_LIST,
    data: result.data.insurances,
  });
};

export const setSelectedPlanData = data => (dispatch) => {
  dispatch({
    type: SET_RETIREMENT_SELECTED_PLAN_DATA,
    data,
  });
};

export const getSessionParams = (retirementForm, retirementDetailForm, selectedPlanData) => (
  {
    sessionParams: {
      email: retirementDetailForm.email,
      client_name: selectedPlanData.info.insurer_slug,
      policy_name: selectedPlanData.info.product_name,
      client_display_name: selectedPlanData.info.insurer_name,
      agree_on_sharing: true,
      session_params: {
        ...parseRetirementForm(retirementForm),
        ph_birth_date: storeStringToDateString(retirementForm.birth_date),
        email: retirementDetailForm.email,
        name: retirementDetailForm.name,
        pb_birth_date: storeStringToDateString(retirementDetailForm.birth_date),
        mobile_number: retirementDetailForm.mobile_number,
        category: 'Retirement',
      },
    },
  }
);
