import Axios from '../lib/axiosFunctions';
import { storeStringToDateString } from '../lib/dateFunctions';

export const GET_ENDOWMENT_PLANS = 'GET_ENDOWMENT_PLANS';
export const GET_ENDOWMENT_PLANS_FAILED = 'GET_ENDOWMENT_PLANS_FAILED';
export const CLEAR_ENDOWMENT_DATA = 'CLEAR_ENDOWMENT_DATA';

export const GET_ENDOWMENT_FIELDS_SUCCESS = 'GET_ENDOWMENT_FIELDS_SUCCESS';
export const GET_ENDOWMENT_FIELDS_FAILED = 'GET_ENDOWMENT_FIELDS_FAILED';

export const GET_ENDOWMENT_VALID_INSURERS_LIST = 'GET_ENDOWMENT_VALID_INSURERS_LIST';
export const GET_ENDOWMENT_VALID_INSURERS_LIST_FAILED = 'GET_ENDOWMENT_VALID_INSURERS_LIST_FAILED';

export const SET_ENDOWMENT_SELECTED_PLAN_DATA = 'SET_ENDOWMENT_SELECTED_PLAN_DATA';

export const parseEndowmentForm = (endowmentForm, insurers = null) => {
  const insurersToUse = insurers ? { insurers } : {};
  return {
    ...insurersToUse,
    ...endowmentForm,
    birth_date: storeStringToDateString(endowmentForm.birth_date),
  };
};

export const getEndowmentFields = () => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/5/formfields`;

  const result = await Axios.GET(url);

  if (
    !result ||
    result.status !== 200
  ) {
    return dispatch({
      type: GET_ENDOWMENT_FIELDS_FAILED,
      data: 'Endowment Fields data invalid',
    });
  }

  // console.log(resultTerm.data);

  return dispatch({
    type: GET_ENDOWMENT_FIELDS_SUCCESS,
    data: result.data,
  });
};

export const clearEndowmentData = () => (dispatch) => {
  dispatch({
    type: CLEAR_ENDOWMENT_DATA,
  });
};

export const getEndowmentPlans = params => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/5/compare`;

  const result = await Axios.POST(url, params);

  // console.log(result);
  if (
    !result ||
    result.status !== 201
  ) {
    return dispatch({
      type: GET_ENDOWMENT_PLANS_FAILED,
      data: 'Get pet plans failed',
    });
  }

  return dispatch({
    type: GET_ENDOWMENT_PLANS,
    data: result.data.insurances,
  });
};

export const getValidInsurersList = params => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/5/insurers`;

  const result = await Axios.POST(url, params);

  // console.log(result);
  if (
    !result ||
    result.status !== 201
  ) {
    return dispatch({
      type: GET_ENDOWMENT_VALID_INSURERS_LIST_FAILED,
      data: 'Get Endowment insurer list failed',
    });
  }

  return dispatch({
    type: GET_ENDOWMENT_VALID_INSURERS_LIST,
    data: result.data.insurances,
  });
};

export const setSelectedPlanData = data => (dispatch) => {
  dispatch({
    type: SET_ENDOWMENT_SELECTED_PLAN_DATA,
    data,
  });
};

export const getSessionParams = (endowmentForm, endowmentDetailForm, selectedPlanData) => (
  {
    sessionParams: {
      email: endowmentDetailForm.email,
      client_name: selectedPlanData.info.insurer_slug,
      policy_name: selectedPlanData.info.product_name,
      client_display_name: selectedPlanData.info.insurer_name,
      agree_on_sharing: true,
      session_params: {
        ...parseEndowmentForm(endowmentForm),
        ph_birth_date: storeStringToDateString(endowmentForm.birth_date),
        email: endowmentDetailForm.email,
        name: endowmentDetailForm.name,
        pb_birth_date: storeStringToDateString(endowmentDetailForm.birth_date),
        mobile_number: endowmentDetailForm.mobile_number,
        category: 'Endowment',
      },
    },
  }
);
