export const SET_NAV_COLOR = 'SET_NAV_COLOR';
export const SET_NAV_BG_COLOR = 'SET_NAV_BG_COLOR';

export const SET_STATE_INSURANCE = 'SET_STATE_INSURANCE';
export const SET_STATE_MORE = 'SET_STATE_MORE';
export const SET_STATE_BUSINESS = 'SET_STATE_BUSINESS';

export const SET_CATEGORY_NAV = 'SET_CATEGORY_NAV';

export const LOGIN_STATE = 'LOGIN_STATE';

export const navColors = {
  GREEN: 'green-font-col',
  WHITE: 'white-font-col',
  NONE: 'display-none',
};


export const navBg = {
  WHITE: 'nav-white-bg',
  NONE: 'nav-no-bg',
};

// page indication
export const onPageInsurance = {
  ACTIVE: 'page-indication',
  NONE: '',
};
export const onPageMore = {
  ACTIVE: 'page-indication',
  NONE: '',
};
export const onPageBusiness = {
  ACTIVE: 'page-indication',
  NONE: '',
};

// login state = false (user not logged in)
// login state = true (user logged in)
export const loginState = {
  LOGIN: true,
};

export const setNavColor = color => (dispatch) => {
  dispatch({
    type: SET_NAV_COLOR,
    data: color,
  });
};

export const setNavBGColor = bgcolor => (dispatch) => {
  dispatch({
    type: SET_NAV_BG_COLOR,
    data: bgcolor,
  });
};


export const setStateInsurance = pageState => (dispatch) => {
  dispatch({
    type: SET_STATE_INSURANCE,
    data: pageState,
  });
};
export const setStateMore = pageState => (dispatch) => {
  dispatch({
    type: SET_STATE_MORE,
    data: pageState,
  });
};
export const setStateBusiness = pageState => (dispatch) => {
  dispatch({
    type: SET_STATE_BUSINESS,
    data: pageState,
  });
};

export const checkLoginState = userState => (dispatch) => {
  dispatch({
    type: LOGIN_STATE,
    data: userState,
  });
};

export const setCategoryNavBar = (isCategory, isMain = false, isBusiness = false,) => (dispatch) => {
  dispatch({
    type: SET_CATEGORY_NAV,
    data: { isCategory, isMain, isBusiness },
  });
};
