import {
  SET_LOGIN_POPUP,
} from '../actions/loginActions';

const initialState = {
  popup: null,
  loginContainer: null,
};

const login = (state = initialState, action) => {
  switch (action.type) {
    case SET_LOGIN_POPUP:
      return {
        ...state,
        popup: action.data.popup,
        loginContainer: action.data.loginContainer,
      };
    default:
      return state;
  }
};

export default login;
