import _ from 'lodash';
import moment from 'moment';
import Axios from 'lib/axiosFunctions';
import { defaultMainFields, initialCountries } from 'containers/Travel/Travel.constants';
import { dateToString } from 'lib/dateFunctions';
import { getFormValue } from 'lib/helperFunctions';

export const GET_TRAVEL_MAIN_FORM_FIELDS_SUCCESS = 'GET_MAIN_FORM_FIELDS_SUCCESS';
export const GET_TRAVEL_MAIN_FORM_FIELDS_FAILED = 'GET_MAIN_FORM_FIELDS_FAILED';

export const parseTravelForm = (values) => {
  const {
    countries,
    region,
    end_date: endDate,
    num_adult: numAdult,
    num_children: numChildren,
    plan_type: planType,
    start_date: startDate,
    trip_type: tripType,
  } = values;

  const startDateParsed = dateToString(startDate && startDate.value);
  const endDateParsed = dateToString(endDate && endDate.value);

  const params = {
    trip_type: tripType && tripType.value,
    plan_type: planType && planType.value,
    start_date: startDateParsed,
  };

  if (params.trip_type === 'Single Trip') {
    params.countries = _.map(countries && countries.value, value => value.value);
    params.end_date = endDateParsed;
  } else if (params.trip_type === 'Annual Multi-Trip') {
    params.region = region && region.value.value;
  }

  if (params.plan_type === 'Individual') {
    params.num_adult = '1';
  } else if (params.plan_type === 'Family') {
    params.num_adult = ((numAdult && numAdult.value) || 0).toString();
    params.num_children = ((numChildren && numChildren.value) || 0).toString();
  } else if (params.plan_type === 'Group') {
    params.num_adult = ((numAdult && numAdult.value) || 0).toString();
  }

  // if (insurers) {
  //   params.insurers = insurers;
  // }

  return params;
};


const parseRelationship = (relationship, childCount) => {
  if (relationship === 'My Child') {
    return { relationship: `My Child ${childCount}`, childCount: childCount + 1 };
  }

  return { relationship, childCount };
};

export const getPersons = (values) => {
  if (getFormValue(values.plan_type) === 'Family') {
    return { num_adult: getFormValue(values.num_adult).toString(), num_children: getFormValue(values.num_children).toString() };
  }
  if (getFormValue(values.plan_type) === 'Group') {
    return { num_adult: getFormValue(values.num_adult).toString(), num_children: '0' };
  }
  // Individual
  return { num_adult: '1', num_children: '0' };
};

export const getSessionParams = (values, detailValues, selectedPlanData, planType) => {
  let currChildCount = 1;


  const getAdultsChildren = (isAdult) => {
    const result = [];
    const count = isAdult ? values.num_adult.value : values.num_children.value;
    const startValue = isAdult ? 1 : 0;

    for (let i = startValue; i < count; ++i) {
      const key = isAdult ? `adult_${i}` : `child_${i}`;
      const currValues = detailValues[key];

      if (currValues) {
        const relationship = parseRelationship(getFormValue(currValues.relationship), currChildCount);
        currChildCount = relationship.childCount;

        const mobileNumber = isAdult ? { mobile_number: getFormValue(currValues.mobile_number) } : {};

        result.push({
          name: getFormValue(currValues.name),
          id_type: getFormValue(currValues.id_type),
          nric: getFormValue(currValues.nric),
          citizenship: getFormValue(currValues.nationality),
          birth_date: moment(getFormValue(currValues.birth_date)).format('DD/MM/YYYY'),
          gender: getFormValue(currValues.gender),
          ...mobileNumber,
          relationship: relationship.relationship,
        });
      }
    }

    return result;
  };


  const getIndividual = (individualValues, withBlkInsurers) => {
    const blkField = withBlkInsurers ? { blk_no: getFormValue(individualValues.blk_no) } : {};

    return ({
      email: getFormValue(individualValues.email),
      nric: getFormValue(individualValues.nric),
      citizenship: getFormValue(individualValues.nationality),
      given_name: getFormValue(individualValues.given_name),
      family_name: getFormValue(individualValues.family_name),
      id_type: getFormValue(individualValues.id_type),
      birth_date: moment(getFormValue(individualValues.birth_date)).format('DD/MM/YYYY'),
      gender: getFormValue(individualValues.gender),
      race: getFormValue(individualValues.race),
      marital_status: getFormValue(individualValues.marital_status),
      occupation: getFormValue(individualValues.occupation),
      mobile_number: getFormValue(individualValues.mobile_number),
      address: getFormValue(individualValues.address),
      unit_no: getFormValue(individualValues.unit_no),
      postal: getFormValue(individualValues.postal),
      ...blkField,
    });
  };

  const getOtherDetails = () => {
    if (values.plan_type.value === 'Family') {
      return { family: [...getAdultsChildren(true), ...getAdultsChildren(false)] };
    }

    if (values.plan_type.value === 'Group') {
      return { group: getAdultsChildren(true) };
    }

    return {};
  };

  const getCountries = () => {
    if (values.trip_type.value === 'Single Trip') {
      return { countries: getFormValue(values.countries) };
    }

    return { destination: getFormValue(values.region) };
  };

  const getEndDate = () => {
    if (values.trip_type.value === 'Annual Multi-Trip') {
      return { end_date: '' };
    }

    return { end_date: moment(getFormValue(values.end_date)).format('DD/MM/YYYY') || '' };
  };

  try {
    const individualValues = detailValues.adult_0;

    const isEtiqa = selectedPlanData.info.insurer_slug === 'etiqa' || selectedPlanData.info.insurer_slug === 'tiq';
    const isMsig = selectedPlanData.info.insurer_slug === 'msig';
    const withBlkInsurers = isEtiqa || isMsig;

    const { country, destination, quote_reference: quoteReference } = selectedPlanData.policy_info[planType].premium;
    const countryParam = country ? { country } : {};
    const destinationParam = destination ? { destination } : {};

    return ({
      agree_on_sharing: true,
      client_name: selectedPlanData.info.insurer_slug,
      client_display_name: selectedPlanData.info.insurer_name,
      policy_name: selectedPlanData.info.product_name,
      email: getFormValue(individualValues.email),
      nric: getFormValue(individualValues.nric),
      session_params: {
        ...getIndividual(individualValues, withBlkInsurers),
        ...getPersons(values),
        ...getCountries(),
        ...countryParam,
        ...destinationParam,
        plan_type: getFormValue(values.plan_type),
        policy_name: selectedPlanData.info.product_name,
        trip_type: getFormValue(values.trip_type),
        policy_type: _.lowerCase(planType),
        start_date: moment(getFormValue(values.start_date)).format('DD/MM/YYYY'),
        ...getEndDate(),
        ...getOtherDetails(),
        quote_reference: quoteReference || '',
        premium: selectedPlanData.policy_info[planType].premium.net_price,
        category: 'Travel',
      },
    });
  } catch (error) {
    return null;
  }
};

export const getTravelPlans = async (params) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/7/compare?all=true`;

  const result = await Axios.POST(url, params);

  if (result && result.status === 201 && result.data) {
    return result.data;
  }

  return null;
};

