export const getMainFormFields = constants => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/7/formfields`;

  const result = await Axios.GET(url);

  if (constants) {
    const mainFields = [...defaultMainFields];

    let countries = [...constants.countries];
    _.remove(countries, item => _.indexOf(initialCountries, item) !== -1);
    countries = [...initialCountries, ...countries];

    _.each(mainFields, (field) => {
      if (field.options_constant_key && field.options_constant_key === 'countries') {
        // eslint-disable-next-line
        field.options = _.map(countries, option => ({ display_name: option, value: option }));
      }
    });


    dispatch({
      type: GET_TRAVEL_MAIN_FORM_FIELDS_SUCCESS,
      data: mainFields,
    });

    return mainFields;
  }

  if (!result || (result && result.status !== 200)) {
    dispatch({
      type: GET_TRAVEL_MAIN_FORM_FIELDS_FAILED,
      data: 'Get Travel Main form fields failed',
    });

    return null;
  }

  let fields = null;
  try {
    fields = _.reduce(result.data.form_fields, (reduceResult, field) => {
      const newArray = reduceResult[field.name] || [];
      newArray.push(field);
      const newResult = {
        ...reduceResult,
        [field.name]: newArray,
      };

      return newResult;
    }, {});
  } catch (error) {
    dispatch({
      type: GET_TRAVEL_MAIN_FORM_FIELDS_FAILED,
      data: 'Parse travel form fields failed',
    });

    return null;
  }

  dispatch({
    type: GET_TRAVEL_MAIN_FORM_FIELDS_SUCCESS,
    data: fields,
  });

  return fields;
};


export const validateRestrictions = async (params) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/7/validate_restrictions`;

  const result = await Axios.POST(url, params);

  if (
    !result ||
    result.status !== 201
  ) {
    return null;
  }

  return result.data;
};

export const postToRedirect = async (values, detailValues, selectedPlanData, planType) => {
  try {
    const params = getSessionParams(values, detailValues, selectedPlanData, planType);
    const url = `${process.env.REACT_APP_API_HOST}/v2/anonymous_sessions/webview`;

    const result = await Axios.POST(url, params);

    if (result.status === 200 && result.data && result.data.url) {
      return result.data;
    }
  } catch (error) {
    console.log('Post failed');
  }

  return null;
};
