import {
  GET_BLOG_FEED_SUCCESS,
  GET_BLOG_FEED_FAIL,
  GET_BLOG,
} from '../actions/blogActions';

const initialState = {
  blogData: null,

  blogs: [],
  isGettingBlogs: false,
  getBlogsError: '',
};

const blog = (state = initialState, action) => {
  switch (action.type) {
    case GET_BLOG_FEED_SUCCESS:
    case GET_BLOG_FEED_FAIL:
      return { ...state, blogData: action.data };
    // 
    case `${GET_BLOG}_STARTED`:
      return {
        ...state,
        isGettingBlogs: true,
        getBlogsError: '',
      };
    case `${GET_BLOG}_SUCCESS`:
      return {
        ...state,
        blogs: action.payload || [],
        isGettingBlogs: false,
      };
    case `${GET_BLOG}_FAILURE`:
      return {
        ...state,
        getBlogsError: action.payload || '',
        isGettingBlogs: false,
      };
    default:
      return state;
  }
};

export default blog;
