import 'bootstrap/scss/bootstrap.scss';

import React, { Fragment } from 'react';
import { render } from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import reduxThunk from 'redux-thunk';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import WebFont from 'webfontloader';
import $ from 'jquery';
// import TagManager from 'react-gtm-module';

import './index.scss';
import App from './components/App';
import GoToAppStore from './components/GoToAppStore/GoToAppStore';
import Intercom from './components/Intercom/Intercom';

import reducers from './reducers';

// registerServiceWorker,
import { unregister } from './registerServiceWorker';

// import { GTM_STG } from './constants';

if (window) {
  window.$ = $;
}

WebFont.load({
  google: {
    families: [
      'Open Sans:300,300i,400,400i,600,600i,700,700i,800,800i', 'sans-serif',
      'Lato:400', 'sans-serif',
    ],
  },
});

// TagManager.initialize({
//   gtmId: GTM_STG,
//   dataLayerName: 'dataLayer',
// });

const store = createStore(reducers, {}, applyMiddleware(reduxThunk));

const rootElement = document.getElementById('root');
const JSX = (
  <Provider store={store}>
    <BrowserRouter>
      <Fragment>
        <Switch>
          <Route exact path="/gotoappstore" component={GoToAppStore} />
          <Route exact path="/intercom" component={Intercom} />
          <Route component={App} />
        </Switch>
      </Fragment>
    </BrowserRouter>
  </Provider>
);

render(JSX, rootElement);

// registerServiceWorker();
unregister();
