import {
  // set navigation links color
  navColors,
  SET_NAV_COLOR,
  // set navigation bg color
  navBg,
  SET_NAV_BG_COLOR,
  // set insurance state
  onPageInsurance,
  SET_STATE_INSURANCE,
  // set more state
  onPageMore,
  SET_STATE_MORE,
  // set business page state
  onPageBusiness,
  SET_STATE_BUSINESS,
  // check user login state
  loginState,
  LOGIN_STATE,
  SET_CATEGORY_NAV,
} from '../actions/navActions';

const initialState = {
  navColors: navColors.GREEN,
  navBg: navBg.WHITE,
  onPageInsurance: onPageInsurance.NONE,
  onPageMore: onPageMore.NONE,
  onPageBusiness: onPageBusiness.NONE,
  loginState: loginState.LOGIN,
  dotArray: [],
  isCategory: false,
  isMain: false,
  isBusiness: false,
};

const nav = (state = initialState, action) => {
  switch (action.type) {
    case SET_NAV_COLOR:
      return { ...state, navColors: action.data };
    case SET_NAV_BG_COLOR:
      return { ...state, navBg: action.data };
    case SET_STATE_INSURANCE:
      return { ...state, onPageInsurance: action.data };
    case SET_STATE_MORE:
      return { ...state, onPageMore: action.data };
    case SET_STATE_BUSINESS:
      return { ...state, onPageBusiness: action.data };
    case LOGIN_STATE:
      return { ...state, loginState: action.data };
    case SET_CATEGORY_NAV:
      return {
        ...state,
        isCategory: action.data.isCategory,
        isMain: action.data.isMain,
        isBusiness: action.data.isBusiness,
      };
    default:
      return state;
  }
};

export default nav;
