import {
  SET_RESTRICTION_POPUP,
  CLEAR_RESTRICTION_POPUP,
} from '../actions/restrictionsActions';

const initialState = {
  data: null,
};

const restrictions = (state = initialState, action) => {
  switch (action.type) {
    case SET_RESTRICTION_POPUP:
      return {
        ...state,
        data: action.data,
      };
    case CLEAR_RESTRICTION_POPUP:
      return initialState;
    default:
      return state;
  }
};

export default restrictions;
