import {
  CLEAR_MOTOR_DATA,
  GET_MOTOR_PLANS_SUCCESS,
  // GET_MOTOR_PLANS_FAILED,
  GET_MOTOR_FIELDS_SUCCESS,
  SET_SELECTED_INSURER_INDEX,
  SET_MOTOR_SELECTED_PLAN_DATA,
} from '../actions/motorActions';

const initialState = {
  motorFields: null,
  plansData: null,
  selectedIndex: 0,
  selectedPlanData: null,
};

const motor = (state = initialState, action) => {
  switch (action.type) {
    case GET_MOTOR_FIELDS_SUCCESS:
      return { ...state, motorFields: action.data };
    case GET_MOTOR_PLANS_SUCCESS:
      return { ...state, plansData: action.data };
    case CLEAR_MOTOR_DATA:
      return { ...initialState, motorFields: state.motorFields };
    case SET_SELECTED_INSURER_INDEX:
      return { ...state, selectedIndex: action.data };
    case SET_MOTOR_SELECTED_PLAN_DATA:
      return { ...state, selectedPlanData: action.data };
    default:
      return state;
  }
};

export default motor;
