import { storeStringToDateString } from '../lib/dateFunctions';
import Axios from '../lib/axiosFunctions';

export const GET_PET_PLANS = 'GET_PET_PLANS';
export const GET_PET_PLANS_FAILED = 'GET_PET_PLANS_FAILED';
export const CLEAR_PET_DATA = 'CLEAR_PET_DATA';

export const GET_PET_FIELDS_SUCCESS = 'GET_PET_FIELDS_SUCCESS';
export const GET_PET_FIELDS_FAILED = 'GET_PET_FIELDS_FAILED';

export const SET_PET_SELECTED_PLAN_DATA = 'SET_PET_SELECTED_PLAN_DATA';

export const getPetFields = () => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/16/formfields`;

  const result = await Axios.GET(url);

  if (
    !result ||
    result.status !== 200
  ) {
    return dispatch({
      type: GET_PET_FIELDS_FAILED,
      data: 'Pet Fields data invalid',
    });
  }

  // console.log(result.data);

  return dispatch({
    type: GET_PET_FIELDS_SUCCESS,
    data: result.data,
  });
};

export const clearPetData = () => (dispatch) => {
  dispatch({
    type: CLEAR_PET_DATA,
  });
};

export const getPetPlans = () => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/16/compare`;

  const result = await Axios.POST(url, {
    insurer: 'liberty',
  });

  // console.log(result);
  if (
    !result ||
    result.status !== 201
  ) {
    return dispatch({
      type: GET_PET_PLANS_FAILED,
      data: 'Get pet plans failed',
    });
  }

  return dispatch({
    type: GET_PET_PLANS,
    data: result.data.insurances,
  });
};

export const setSelectedPlanData = data => (dispatch) => {
  dispatch({
    type: SET_PET_SELECTED_PLAN_DATA,
    data,
  });
};

export const getSessionParams = (petForm, petDetailsForm, selectedPlanData) => {
  const params = {
    nric: petDetailsForm.nric,
    agree_on_sharing: true,
    client_name: selectedPlanData.info.insurer_slug,
    policy_name: selectedPlanData.info.product_name,
    client_display_name: selectedPlanData.info.insurer_name,
    session_params: {
      ...petForm,
      ...petDetailsForm,
      start_date: storeStringToDateString(petForm.start_date),
      pet_birth_date: storeStringToDateString(petDetailsForm.pet_birth_date),
      client_name: selectedPlanData.info.insurer_slug,
      policy_name: selectedPlanData.info.product_name,
      policy_type: selectedPlanData.planType,
      category: 'Pet',
    },
    email: petDetailsForm.email,
  };

  // console.log(params);

  return params;
};
