import {
  SET_PLAN_TYPE,
  SET_SELECTED_INDEX,
  SET_SELECTED_INSURERS,
  CLEAR_PLAN_TYPE,
  CLEAR_SELECTED_INSURERS,
  RESET_COMPARE,
} from '../actions/compareActions';

const initialState = {
  planType: null,
  selectedInsurers: null,
  selectedIndex: null,
};

const compare = (state = initialState, action) => {
  switch (action.type) {
    case SET_PLAN_TYPE:
      return {
        ...state,
        planType: action.data,
      };
    case SET_SELECTED_INSURERS:
      return {
        ...state,
        selectedInsurers: action.data,
      };
    case SET_SELECTED_INDEX:
      return {
        ...state,
        selectedIndex: action.data,
      };
    case CLEAR_PLAN_TYPE:
      return {
        ...state,
        planType: null,
      };
    case CLEAR_SELECTED_INSURERS:
      return {
        ...state,
        selectedInsurers: null,
      };
    case RESET_COMPARE:
      return initialState;
    default:
      return state;
  }
};

export default compare;
