import _ from 'lodash';
import { storeStringToDateString, storeStringToDate } from './dateFunctions';
import { tldExists } from 'tldjs';

const nric = [];
nric.multiples = [2, 7, 6, 5, 4, 3, 2];

nric.isNricValid = (theNric) => {
  if (!theNric || theNric === '') {
    return false;
  }

  if (theNric.length !== 9) {
    return false;
  }

  let total = 0;
  let count = 0;
  let numericNric;
  const first = theNric[0];
  const last = theNric[theNric.length - 1];

  if (first !== 'S' && first !== 'T') {
    return false;
  }

  numericNric = theNric.substr(1, theNric.length - 2);

  if (_.isNaN(numericNric)) {
    return false;
  }

  while (numericNric !== 0) {
    total += (numericNric % 10) * nric.multiples[nric.multiples.length - (1 + count)];
    count += 1;
    numericNric /= 10;
    numericNric = Math.floor(numericNric);
  }

  let outputs;
  if (first === 'S') {
    outputs = ['J', 'Z', 'I', 'H', 'G', 'F', 'E', 'D', 'C', 'B', 'A'];
  } else {
    outputs = ['G', 'F', 'E', 'D', 'C', 'B', 'A', 'J', 'Z', 'I', 'H'];
  }

  return last === outputs[total % 11];
};

nric.isFinValid = (fin) => {
  if (!fin || fin === '') {
    return false;
  }

  if (fin.length !== 9) {
    return false;
  }

  let total = 0;
  let count = 0;
  let numericNric;
  const first = fin[0];
  const last = fin[fin.length - 1];

  if (first !== 'F' && first !== 'G') {
    return false;
  }

  numericNric = fin.substr(1, fin.length - 2);

  if (_.isNaN(numericNric)) {
    return false;
  }

  while (numericNric !== 0) {
    total += (numericNric % 10) * nric.multiples[nric.multiples.length - (1 + count)];
    count += 1;

    numericNric /= 10;
    numericNric = Math.floor(numericNric);
  }

  let outputs;
  if (first === 'F') {
    outputs = ['X', 'W', 'U', 'T', 'R', 'Q', 'P', 'N', 'M', 'L', 'K'];
  } else {
    outputs = ['R', 'Q', 'P', 'N', 'M', 'L', 'K', 'X', 'W', 'U', 'T'];
  }

  return last === outputs[total % 11];
};

// Validate a form value
export const validateFormValue = (key, value, customValidators = {}, extraInfo = {}) => {
  const splittedKey = _.split(key, '-');
  const identifier = splittedKey[splittedKey.length - 1];

  switch (identifier) {
    // quote form
    case 'dateOfBirth':
    case 'birth_date':
    case 'start_date':
    case 'end_date':
    case 'commencement_date':
    case 'pass_date':
    case 'pet_birth_date': {
      const reDate = /^\d{1,2}\/\d{1,2}\/\d{4}$/;
      if (!reDate.test(storeStringToDateString(value))) {
        return { result: false, error: 'Invalid date format' };
      }
      break;
    }
    case 'email': {
      const reEmail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (!reEmail.test(value) || !tldExists(value) || value.length > 60) {
        return { result: false, error: 'Invalid email' };
      }
      break;
    }
    case 'nric': {
      const reNric = /^[s|g|f|t]\d{7}[a-z]$/i;
      const isNRIC = extraInfo.idType && _.includes(extraInfo.idType, 'NRIC');
      const isNRICPr = extraInfo.idType && _.includes(extraInfo.idType, 'NRIC (Singapore PR)');
      const isFIN = extraInfo.idType && !isNRIC;

      let nricCheck = false;

      if (isNRIC && !isNRICPr && extraInfo.nationality) {
        nricCheck = extraInfo.nationality === 'Singaporean';
      } else {
        nricCheck = true;
      }

      if (
        !nricCheck ||
        (isFIN && extraInfo.nationality === 'Singaporean')
      ) {
        return { result: false, error: 'Nationality & type of ID mismatch' };
      }

      let result = false;
      const valueToUse = value.toUpperCase();
      if (isNRIC) {
        result = nric.isNricValid(valueToUse);
      } else if (isFIN) {
        result = nric.isFinValid(valueToUse);
      } else {
        result = nric.isNricValid(valueToUse) || nric.isFinValid(valueToUse);
      }

      if (!reNric.test(valueToUse) || !result) {
        const idType = extraInfo.idType || 'ID Number';
        return { result: false, error: `Invalid ${idType}` };
      }
      break;
    }
    case 'firstName':
    case 'surname':
    case 'name':
    case 'family_name':
    case 'given_name': {
      // [a-zA-Z0-9]
      const reName = /^[^#&<>"~;$^%{}?/|\\=:*/[\]@]{2,30}$/;
      const reFullName = /^[^#&<>"~;$^%{}?/|\\=:*/[\]@]{2,60}$/;

      const testToUse = identifier === 'name' ? reFullName : reName;
      if (!testToUse.test(value)) {
        return { result: false, error: 'Invalid name' };
      }
      break;
    }
    case 'contact':
    case 'mobile_number': {
      // const reMobile = /^[3689]\d{7}$/;
      const reMobile = /^[89]\d{7}$/;
      if (!reMobile.test(value)) {
        return { result: false, error: 'Invalid mobile number' };
      }
      break;
    }
    case 'postal': {
      const reNum = /^\d+$/;
      // value.length < 3
      if (value.length !== 6 || !reNum.test(value)) {
        return { result: false, error: 'Invalid postal' };
      }
      break;
    }
    case 'marital_status': {
      return { result: value.toUpperCase() === 'SINGLE' || value.toUpperCase() === 'MARRIED', error: 'Invalid marital status' };
    }
    case 'gender': {
      return { result: value.toUpperCase() === 'MALE' || value.toUpperCase() === 'FEMALE', error: 'Invalid gender' };
    }
    case 'address': {
      // @#%&*()-_+',./
      const reAddress = /^[^<>"~;$^{}?|\\=:[\]]{1,30}$/;
      const reNum = /^\d+$/;

      if (value && value.length > 30) {
        return { result: false, error: 'Max character limit of 30 exceeded' };
      }

      if (extraInfo && extraInfo.insurer_slug === 'msig') {
        const splittedString = _.split(value, ' ');
        if (splittedString && splittedString[0] && reNum.test(splittedString[0])) {
          return { result: false, error: 'First word in address cannot be a number' };
        }
      }

      if (!reAddress.test(value)) {
        return { result: false, error: 'Invalid characters in address' };
      }

      break;
    }
    case 'blk_no':
    case 'unit_no': {
      const reNum = /^\d+$/;
      const reName = /^[^&<>"~;$^%{}?/|\\=:*/[\]@]{1,20}$/;
      const msg = identifier === 'blk_no' ? 'block number' : 'unit number';
      if (!reName.test(value)) {
        return { result: false, error: `Invalid ${msg}` };
      }
      if (identifier === 'blk_no' && _.includes(value, '#')) {
        return { result: false, error: 'Cannot include # symbol' };
      }
      if (identifier === 'unit_no' && extraInfo && extraInfo.insurer_slug === 'etiqa' && _.includes(value, '#')) {
        return { result: false, error: 'Cannot include # symbol' };
      }
      if (identifier === 'unit_no' && reNum.test(value)) {
        return { result: false, error: 'Unit number cannot be fully numeric' };
      }
      break;
    }
    default: {
      const customValidator = customValidators[identifier];
      if (customValidator) {
        if (customValidator.type === 'ARRAY') {
          const isExists = _.findIndex(customValidator.data, data => data === value) !== -1;
          return { result: isExists, error: 'Invalid value' };
        }

        if (customValidator.type === 'DATE') {
          const date = storeStringToDate(value);
          if (!date || date === -1) {
            return { result: false, error: 'Invalid date' };
          }

          let isValidStart = true;
          let isValidEnd = true;
          if (customValidator.start) {
            isValidStart = customValidator.start <= date;
          }

          if (customValidator.end) {
            isValidEnd = date <= customValidator.end;
          }

          return { result: isValidStart && isValidEnd, error: 'Date out of range' };
        }

        if (customValidator.type === 'LENGTH') {
          return { result: value && value.length <= customValidator.data, error: 'Max character limit of 30 exceeded' };
        }
      }
      break;
    }
  }

  return { result: true };
};

// Use this to validate the form
export const validateForm = (form, fieldsToCheck, customValidators, extraInfo = {}) => {
  if (!form) {
    return { form: 'Form Error' };
  }

  return (
    _.reduce(fieldsToCheck, (result, field) => {
      let prefix = '';
      const dashPos = _.findLastIndex(field, char => char === '-');
      if (dashPos !== -1) {
        prefix = `${field.substr(0, dashPos)}-`;
      }

      const newResult = result;
      const idType = form.id_type ? { idType: form[`${prefix}id_type`] } : {};
      const nationality = form.nationality ? { nationality: form[`${prefix}nationality`] } : {};

      try {
        if (!form ||
          form[field] === null ||
          form[field] === undefined ||
          (form[field].constructor === String && form[field].trim() === '') ||
          (form[field].constructor === Array && _.isEmpty(form[field]))) {
          newResult[field] = 'Field not entered!';
        } else {
          const validateResult = validateFormValue(field, form[field], customValidators, { ...idType, ...nationality, ...extraInfo });
          if (!validateResult.result) {
            newResult[field] = validateResult.error;
          }
        }
      } catch (error) {
        newResult[field] = 'Field error! Try again.';
      }

      return newResult;
    }, {})
  );
};

export const dateReValidate = (isEmptyResult, isFirstValidate, errorData, setState, callback) => {
  const isDateInResult = _.find(errorData, data => _.includes(data, 'date'));
  if (!isEmptyResult && isDateInResult && isFirstValidate) {
    const newErrorData = { ...errorData };
    _.each(newErrorData, (value, key) => {
      if (_.includes(key, 'date')) {
        delete newErrorData[key];
      }
    });

    if (!_.isEqual(newErrorData, errorData)) {
      setState(newErrorData);
    }

    setTimeout(() => callback(), 200);
    return true;
  }

  return false;
};
