import {
  EDIT_SUBMITTED,
  RESET_EDIT_SUBMITTED,
} from '../actions/editFormActions';

const initialState = {
  isRevert: null,
  isEditSubmitted: false,
};

const editForm = (state = initialState, action) => {
  switch (action.type) {
    case EDIT_SUBMITTED:
      return { ...state, isEditSubmitted: true };
    case RESET_EDIT_SUBMITTED:
      return { ...state, isEditSubmitted: false };
    default:
      return state;
  }
};

export default editForm;
