import {
  SET_TAB_SELECTION,
} from '../actions/tabActions';

const initialState = {
};

const tab = (state = initialState, action) => {
  switch (action.type) {
    case SET_TAB_SELECTION:
      return { ...state, [action.data.tabName]: action.data.selection };
    default:
      return state;
  }
};

export default tab;
