import _ from 'lodash';
import Axios from '../lib/axiosFunctions';

import { storeStringToDateString } from '../lib/dateFunctions';

export const GET_MOTOR_FIELDS_FAILED = 'GET_MOTOR_FIELDS_FAILED';
export const GET_MOTOR_FIELDS_SUCCESS = 'GET_MOTOR_FIELDS_SUCCESS';

export const CLEAR_MOTOR_DATA = 'CLEAR_MOTOR_DATA';
export const GET_MOTOR_PLANS_SUCCESS = 'GET_MOTOR_PLANS_SUCCESS';
export const GET_MOTOR_PLANS_FAILED = 'GET_MOTOR_PLANS_FAILED';
export const SET_SELECTED_INSURER_INDEX = 'SET_SELECTED_INSURER_INDEX';

export const SET_MOTOR_SELECTED_PLAN_DATA = 'SET_MOTOR_SELECTED_PLAN_DATA';


export const getMotorFields = () => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/8/formfields`;

  const result = await Axios.GET(url);

  if (
    !result ||
    result.status !== 200
  ) {
    return dispatch({
      type: GET_MOTOR_FIELDS_FAILED,
      data: 'Motor Fields data invalid',
    });
  }


  const carModels = {};
  const motorFields = {};

  try {
    _.each(result.data.form_fields, (field) => {
      if (field.condition && field.condition.car_brand) {
        carModels[field.condition.car_brand.value] = field.options;
      }

      if (field.name !== '') {
        motorFields[field.name] = field;
      }
    });

    motorFields.car_models.options = carModels;
  } catch (error) {
    return dispatch({
      type: GET_MOTOR_FIELDS_FAILED,
      data: 'Motor Fields parsed error',
    });
  }

  // console.log(motorFields);

  return dispatch({
    type: GET_MOTOR_FIELDS_SUCCESS,
    data: motorFields,
  });
};

export const clearMotorData = () => (dispatch) => {
  dispatch({
    type: CLEAR_MOTOR_DATA,
  });
};

export const getMotorPlans = params => async (dispatch) => {
  dispatch({
    type: CLEAR_MOTOR_DATA,
  });

  // console.log(params);
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/8/compare`;

  const result = await Axios.POST(url, params);

  // console.log(result);
  if (!result || result.status !== 201 || !result.data || !result.data.insurances) {
    return dispatch({
      type: GET_MOTOR_PLANS_FAILED,
      data: result,
    });
  }

  return dispatch({
    type: GET_MOTOR_PLANS_SUCCESS,
    data: result.data.insurances,
  });
};

export const setSelectedInsurerIndex = index => async (dispatch) => {
  dispatch({
    type: SET_SELECTED_INSURER_INDEX,
    data: index,
  });
};

export const setSelectedPlanData = data => (dispatch) => {
  // console.log(data);
  dispatch({
    type: SET_MOTOR_SELECTED_PLAN_DATA,
    data,
  });
};

export const getParams = (selectedPlanData, detailForm, motorForm, motorMoreForm) => {
  // console.log(selectedPlanData);
  return ({
    sessionParams: {
      email: detailForm.email,
      nric: detailForm.nric,
      client_name: selectedPlanData.clientSlug,
      policy_name: selectedPlanData.policyName,
      client_display_name: selectedPlanData.clientName,
      agree_on_sharing: true,
      session_params: {
        ...detailForm,
        ...motorForm,
        ...motorMoreForm,
        year_born: motorMoreForm.year_born.toString(),
        year_registered: motorForm.year_registered.toString(),
        commencement_date: storeStringToDateString(detailForm.commencement_date),
        category: 'Motor',
      },
    },
  });
};


export const postToFWD = async (popupData, motorForm, motorMoreForm) => {
  const params = {
    email: popupData.email,
    client_name: 'fwd',
    policy_name: 'FWD Motor',
    agree_on_sharing: true,
    session_params: {
      policy_type: _.startCase(popupData.category),
      ...motorForm,
      ...motorMoreForm,
      ncd: motorMoreForm.ncd.toString(),
      year_born: motorMoreForm.year_born.toString(),
      year_registered: motorForm.year_registered.toString(),
      years_driving: motorMoreForm.years_driving.toString(),
      demerit_status: motorMoreForm.demerit_status,
      number_of_claims: motorMoreForm.number_of_claims.toString(),
      category: 'Motor',
    },
  };

  const url = `${process.env.REACT_APP_API_HOST}/v2/anonymous_sessions/webview`;

  const result = await Axios.POST(url, params);

  // console.log(result);

  if (result.status === 200 && result.data && result.data.url) {
    window.location = result.data.url;
  }
};
