export const FORMSTATES = {
  MAIN: { index: 0, path: '' },
  MORE: { index: 1, path: '/about' },
  CHOOSECOMPANY: { index: 2, path: '/company' },
  CHOOSEPLAN: { index: 3, path: '/plan' },
  DETAILFORM: { index: 4, path: '/details' },
  CHECKOUT: { index: 5, path: '/review' },
  SUBMITTED: { index: 6, path: '/confirmation' },
};

export const parseData = (
  plansData,
  validInsurersList = null,
  currentInsurers,
  tripType,
  onBuyClicked,
  setSelectedPlanData,
  loadingEnd,
  setRestrictionPopup,
  singleInsurerIndex = null,
  trackingTags = null,
) => {
  const parsedData = {
    plans: {},
    selectedInsurers: [null, null, null],
    validInsurers: [],
  };

  // console.log(currentInsurers, plansData);

  // Get the ordered index based on selected insurers.
  // If singleInsurerIndex, display only 1 insurer, so set it as itself.
  let orderedIndex = null;
  if (singleInsurerIndex !== null) {
    orderedIndex = [singleInsurerIndex];
  } else {
    orderedIndex = _.reduce(currentInsurers, (result, insurer) => {
      const index = _.findIndex(plansData, data => data.info.insurer_slug === insurer);
      if (index !== -1) {
        result.push(index);
      }

      return result;
    }, []);
  }

  // console.log(plansData, singleInsurerIndex);
  // Get possible coverage types keys
  // If singleInsurerIndex, display only itself,
  // so only check itself.
  // If not, the first 1 should have same coverages as the rest.
  parsedData.coverageTypes = singleInsurerIndex !== null ?
    plansData[singleInsurerIndex].coverage_types :
    plansData[0].coverage_types;

  // console.log(parsedData, plansData[singleInsurerIndex]);
  // Get possible coverage types desc (These are the actual tabs)
  parsedData.coverageTypesDescs = [];
  let isInitCoverageTypes = true;

  // Get all possible categories
  _.each(plansData, (data) => {
    parsedData.policyTypes = _.union(parsedData.policyTypes, data.policy_types);
  });

  // Loop all category types [Basic, Essential, Elite]
  _.each(parsedData.policyTypes, (category, categoryIndex) => {
    const categoryToAssign = singleInsurerIndex !== null ? 'default' : category;

    // Loop through all insurers plans
    _.each(orderedIndex, (orderedIdx, index) => {
      const data = plansData[orderedIdx];
      const indexToAssign = singleInsurerIndex !== null ? categoryIndex : index;

      parsedData.selectedInsurers[index] = {
        insurerSlug: data.info.insurer_slug,
        insurerName: data.info.insurer_name,
        productSlug: data.info.product_slug,
        productName: data.info.product_name,
        discount: data.info.discount,
        wording: data.info.wording_url,
        productDetails: data.product_details,
        teaser: data.info.teaser,
      };

      // Add restrictions if show_restrictions
      if (data.show_restrictions && data.restrictions) {
        parsedData.selectedInsurers[index].restrictions = data.restrictions;
      }

      // Check if the category exists for this insurer's plan
      const categoryPlanInfo = data.policy_info[category];
      if (categoryPlanInfo) {
        // Get this category data for this insurer's plan
        parsedData.plans[categoryToAssign] = parsedData.plans[categoryToAssign] || {};
        // Append premium data
        parsedData.plans[categoryToAssign].premium = parsedData.plans[categoryToAssign].premium || [null, null, null];

        // Consolidated data
        const selectedPlanData = {
          policyInfo: _.reduce(data.policy_info, (result, info, key) => {
            const newResult = { ...result, [key]: info.premium };
            return newResult;
          }, {}),
          info: data.info,
          planType: category,
          tripType,
        };

        parsedData.plans[categoryToAssign].premium[indexToAssign] = {
          ...categoryPlanInfo.premium,
          insurerSlug: data.info.insurer_slug,
          cashback: data.info.cashback,
          onClick: () => {
            onBuyClicked(data, category);
            setSelectedPlanData(selectedPlanData);
          },
          doTag: () => {
            if (trackingTags && trackingTags[category]) {
              trackingTags[category](selectedPlanData);
            }
          },
        };

        // Get coverages data of the current category from parsedData
        const coverages = parsedData.plans[categoryToAssign].coverages || {};
        // Loop all data of this insurer's category from API data
        _.each(categoryPlanInfo.coverages, (val, key) => {
          if (isInitCoverageTypes) {
            const coverageIndex = _.findIndex(parsedData.coverageTypes, cat => cat === key);
            if (coverageIndex !== -1) {
              parsedData.coverageTypesDescs[coverageIndex] = val.header || key;
            }
          }
          // Get the content data current stored in parsedData
          const itemList = coverages[key] ? coverages[key].content || [] : [];
          // Loop through all the content of the API data
          _.each(val.content, (item, contentIndex) => {
            // Extract the current stored list item out or create new
            const listItem = itemList[contentIndex] || {
              title: item.title,
              sub_title: item.sub_title,
              description: item.description,
              dropdown: item.dropdown || false,
              subContent: [],
              targets: [null, null, null],
            };

            // Extract sub content if there is dropdown
            if (item.dropdown) {
              const { subContent } = listItem;
              _.each(item.sub_content, (subItem, subIndex) => {
                const subListItem = subContent[subIndex] || {
                  title: subItem.title,
                  description: subItem.description,
                  targets: [null, null, null],
                };

                subListItem.targets[indexToAssign] = {
                  value: subItem.coverage_amount,
                  description: subItem.description,
                };
                if (subContent.length <= subIndex) {
                  subContent.push(subListItem);
                }
              });
            } else {
              // Extract coverage amount for this list item
              listItem.targets[indexToAssign] = {
                value: item.coverage_amount,
                description: item.description,
              };
            }

            // Add list item to array if new
            if (itemList.length <= contentIndex) {
              itemList.push(listItem);
            }
          });

          // Add new coverage data
          coverages[key] = { header: val.header, content: itemList };

          coverages.disclaimer = coverages.disclaimer || {
            title: '',
            targets: [null, null, null],
            dropdown: false,
          };
          if (data.restrictions && data.info.wording_url) {
            coverages.disclaimer.targets[indexToAssign] = {
              value: (
                <p className="please-read">
                  Please
                  read <button className="link" onClick={() => setRestrictionPopup({ values: data.restrictions })}>restrictions</button> on
                  {` ${data.info.product_name}`} and the
                  <a className="link" href={data.info.wording_url} target="__blank"> Policy Wording </a>
                  for more details.
                </p>
              ),
            };
          } else if (data.restrictions) {
            coverages.disclaimer.targets[indexToAssign] = {
              value: (
                <p className="please-read">
                  Please
                  read <button className="link" onClick={() => setRestrictionPopup({ values: data.restrictions })}>restrictions</button> on
                  {` ${data.info.product_name}`}
                </p>
              ),
            };
          } else if (data.info.wording_url) {
            coverages.disclaimer.targets[indexToAssign] = {
              value: (
                <p className="please-read">
                  Please read the
                  <a className="link" href={data.info.wording_url} target="__blank"> Policy Wording </a>
                  for more details.
                </p>
              ),
            };
          }
        });

        isInitCoverageTypes = false;

        // Add category coverages
        parsedData.plans[categoryToAssign].coverages = coverages;
      }
    });
  });

  if (parsedData.coverageTypes) {
    parsedData.coverageTypes = parsedData.coverageTypes.slice(0, parsedData.coverageTypesDescs.length);
  }

  if (validInsurersList) {
    parsedData.validInsurers = _.reduce(validInsurersList, (result, data) => {
      if (data.is_available) {
        result.push(data);
      }
      return result;
    }, []);
  }

  if (singleInsurerIndex !== null && plansData[singleInsurerIndex].policy_details) {
    parsedData.policyDetails = plansData[singleInsurerIndex].policy_details;
  }
  parsedData.tripType = tripType;

  // console.log(parsedData);
  loadingEnd();
  return parsedData;
};

