export const getConstants = () => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/constants`;
  const constants = await Axios.GET(url);

  if (!constants || (constants && constants.status !== 200)) {
    return dispatch({
      type: GET_CONSTANTS_FAILED,
      data: 'Get constants failed',
    });
  }

  const results = {
    gender: [
      'Male',
      'Female',
    ],
    maritalStatus: [
      'Single',
      'Married',
    ],
    race: [
      'Chinese',
      'Malay',
      'Indian',
      'Eurasian',
      'Others',
    ],
    smoker: [
      'Smoker',
      'Non Smoker',
    ],
  };

  try {
    results.countries = _.reduce(constants.data.countries, (result, data) => {
      result.push(data.name);
      return result;
    }, []);

    results.nationalities = _.reduce(constants.data.nationalities, (result, data) => {
      result.push(data.name);
      return result;
    }, []);

    results.occupations = _.reduce(constants.data.occupations, (result, data) => {
      result.push(data.name);
      return result;
    }, []);

    results.relationships = _.reduce(constants.data.relationships, (result, data) => {
      if (data.name !== 'Myself' && !_.includes(data.name, 'Child')) {
        result.push(data.name);
      }
      return result;
    }, []);
    results.relationships.push('My Child');

    results.idTypes = _.reduce(constants.data.id_types, (result, data) => {
      result.push(data.name);
      return result;
    }, []);

    results.industries = _.reduce(constants.data.industries, (result, data) => {
      result.push(data.name);
      return result;
    }, []);
  } catch (error) {
    return dispatch({
      type: GET_CONSTANTS_FAILED,
      data: 'Parse constants failed',
    });
  }

  // console.log(results);

  return dispatch({
    type: GET_CONSTANTS_PASSED,
    data: results,
  });
};

export const getAnonymousSession = (params, isQuoteOnly = false, verifyParams = null) => async (dispatch = () => {}) => {
  // console.log(params);

  dispatch({
    type: NEW_ANONYMOUS_SESSION,
  });

  const invalidConditions = isQuoteOnly ?
    !params.sessionParams :
    !params.sessionParams || !params.amountPayable || !params.price;

  if (invalidConditions) {
    dispatch({
      type: GET_SESSION_PARAMS_FAILED,
      data: 'Parse session params failed',
    });

    return null;
  }

  let url = `${process.env.REACT_APP_API_HOST}/v2/anonymous_sessions`;
  const anonymousResult = await Axios.POST(url, params.sessionParams);
  // console.log(anonymousResult);
  let validParamsCheck = true;
  if (
    verifyParams &&
    anonymousResult &&
    anonymousResult.config &&
    anonymousResult.config.data
  ) {
    for (let i = 0; i < verifyParams.length; ++i) {
      if (!_.includes(anonymousResult.config.data, verifyParams[i])) {
        validParamsCheck = false;
        break;
      }
    }
  }

  if (!anonymousResult ||
    anonymousResult.status !== 200 ||
    !anonymousResult.data ||
    !anonymousResult.data.session_id ||
    !validParamsCheck) {
    dispatch({
      type: GET_SESSION_PARAMS_FAILED,
      data: 'Get anonymous session params failed',
    });

    return null;
  }

  if (!isQuoteOnly) {
    url = `${process.env.REACT_APP_API_HOST}/v2/payments/transactions`;
    const paramsToUse = {
      token: anonymousResult.data.session_id,
      amount_payable: params.amountPayable,
      price: params.price,
      platform: 'web',
    };
    const transactionResult = await Axios.POST(url, paramsToUse);
    // console.log(transactionResult);

    if (!transactionResult ||
      transactionResult.status !== 200) {
      dispatch({
        type: GET_SESSION_PARAMS_FAILED,
        data: 'Get transaction session failed',
      });

      return null;
    }
  }

  dispatch({
    type: GET_SESSION_PARAMS_SUCCESS,
    data: anonymousResult.data.session_id,
  });

  return anonymousResult.data.session_id;
};

export const getTransactionStripeSession = (sessionId, promoCode, userID) => async (dispatch = () => {}) => {
  dispatch({
    type: CLEAR_STRIPE,
  });

  if (!sessionId) {
    dispatch({
      type: GET_STRIPE_FAILED,
      data: 'Invalid Session ID',
    });

    return null;
  }

  const promo = !promoCode || promoCode === '' ? '' : `promo_code=${promoCode}`;
  const reward = userID ? `user_id=${userID}` : '';
  const prefix = promo || reward ? '?' : '';
  const centerfix = promo && reward ? '&' : '';
  const url = `${process.env.REACT_APP_API_HOST}/v2/payments/checkout/${sessionId}${prefix}${promo}${centerfix}${reward}`;

  const checkOutResult = await Axios.GET(url);

  if (!checkOutResult || checkOutResult.status !== 200 || !checkOutResult.data) {
    dispatch({
      type: GET_STRIPE_FAILED,
      data: 'Get checkout session failed',
    });

    return null;
  }

  // console.log(checkOutResult);

  dispatch({
    type: GET_STRIPE_SUCCESS,
    data: checkOutResult.data,
  });

  return checkOutResult.data;
};

export const postCheckout = (sessionId, stripeData, userID) => async (dispatch = () => {}) => {
  if (!stripeData || !sessionId) {
    dispatch({
      type: POST_CHECKOUT_FAILED,
      data: 'Invalid checkout data!',
    });

    return null;
  }

  const bodyFormData = new FormData();
  // console.log(stripeData);
  _.each(stripeData, (data) => {
    if (!userID || (userID && data.name !== 'user_id')) {
      bodyFormData.append(data.name, data.value);
    }
  });

  if (userID) {
    bodyFormData.append('user_id', userID);
  }

  const url = `${process.env.REACT_APP_API_HOST}/v2/payments/checkout/${sessionId}`;
  const checkOutResult = await Axios.POST(url, bodyFormData);

  // console.log(checkOutResult);
  /* for (let pair of bodyFormData.entries()) {
    console.log(pair[0]+ ', ' + pair[1]);
  }

  console.log('user_id', userID); */

  // console.log(checkOutResult);
  if (!checkOutResult || checkOutResult.status !== 200 || !checkOutResult.data) {
    dispatch({
      type: POST_CHECKOUT_FAILED,
      data: 'Failed to charge.',
    });

    return null;
  }

  dispatch({
    type: POST_CHECKOUT_SUCCESS,
    data: checkOutResult.data,
  });

  return checkOutResult.data;
};

export const getPromoCode = (userToken, sessionId, promocode, rewardID) => async (dispatch = () => {}) => {
  if (!sessionId) {
    dispatch({
      type: GET_PROMO_FAILED,
      data: 'No Session ID!',
    });

    return null;
  }

  const url = userToken ?
    `${process.env.REACT_APP_API_HOST}/v2/payments/authorized/transactions/checkout/${sessionId}/promos` :
    `${process.env.REACT_APP_API_HOST}/v2/payments/transactions/${sessionId}/promos`;

  const useReward = rewardID !== null && rewardID !== false;

  const headers = userToken ? {
    PolicyPalAuthorization: `PolicyPalBearer ${userToken}`,
  } : {};

  // console.log(headers);
  let result = await Axios.POST(url, { promo_code: promocode, use_reward_points: useReward }, headers);

  // console.log(result);
  // Get without promocode if failed
  if (!result || result.status !== 200 || !result.data) {
    result = await Axios.POST(url, { promo_code: '', use_reward_points: useReward }, headers);

    if (result.status === 200 && result.data) {
      dispatch({
        type: GET_PROMO_SUCCESS,
        data: result.data,
      });

      return result.data;
    }

    dispatch({
      type: GET_PROMO_FAILED,
      data: 'Invalid Promo Code',
    });

    return null;
  }

  if (!result || result.status !== 200 || !result.data) {
    dispatch({
      type: GET_PROMO_FAILED,
      data: 'Invalid Promo Code',
    });

    return null;
  }

  dispatch({
    type: GET_PROMO_SUCCESS,
    data: result.data,
  });

  return result.data;
};

export const getReceipt = id => async (dispatch = () => {}) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/payments/transactions/${id}/receipt`;
  const result = await Axios.GET(url);

  // console.log(result);

  if (!result || result.status !== 200 || !result.data) {
    dispatch({
      type: GET_PROMO_FAILED,
      data: 'Invalid Receipt',
    });

    return null;
  }

  dispatch({
    type: GET_PROMO_SUCCESS,
    data: { transaction: result.data },
  });

  return result.data;
};

export const getPromoGiveaways = category => async (dispatch = () => {}) => {
  const idList = {
    pa: 2,
    travel: 7,
    motor: 8,
    pet: 16,
    termLife: 4,
    wholeLife: 3,
    retirement: 6,
    endowment: 5,
  };

  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/${idList[category]}/promos/giveaways`;

  const result = await Axios.GET(url);

  if (!result || result.status !== 200 || !result.data || !result.data.giveaway_fields) {
    return dispatch({
      type: GET_PROMO_GIVEAWAY_FAILED,
      data: { category },
    });
  }

  return dispatch({
    type: GET_PROMO_GIVEAWAY_SUCCESS,
    data: { category, value: result.data.giveaway_fields },
  });
};

export const getAllPromoGiveaways = () => async (dispatch = () => {}) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/promos/giveaways`;

  const result = await Axios.GET(url);

  try {
    if (result && result.status === 200 && result.data) {
      const giveaways = result.data.giveaway_fields;
      const finalGiveaways = [];
      const currentDate = moment();
      _.each(giveaways.main, giveaway => {
        const startDate = moment(giveaway.start_at);
        const endDate = moment(giveaway.end_at);
        if (startDate.isBefore(currentDate) && currentDate.isBefore(endDate)) {
          finalGiveaways.push(giveaway);
        }
      });

      return dispatch({
        type: GET_ALL_PROMO_GIVEAWAY_SUCCESS,
        data: finalGiveaways,
      });
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return dispatch({
    type: GET_ALL_PROMO_GIVEAWAY_FAILED,
  });
};

export const getAdhocTransaction = async (id) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/payments/transactions/${id}`;

  const result = await Axios.GET(url);

  // console.log(result);

  if (!result || result.status !== 200 || !result.data) {
    return null;
  }

  return {
    ...result.data,
    data: JSON.parse(result.data.data),
  };
};

