import { combineReducers } from 'redux';
import { reducer as reduxFormReducer } from 'redux-form';
import blogReducer from './blogReducer';
import navReducer from './navReducer';
import editFormReducer from './editFormReducer';
import userReducer from './userReducer';
import insuranceReducer from './insuranceReducer';
import tabReducer from './tabReducer';
import travelReducer from './travelReducer';
import loadingScreenReducer from './loadingScreenReducer';
import compareReducer from './compareReducer';
import motorReducer from './motorReducer';
import petReducer from './petReducer';
import paReducer from './paReducer';
import loginReducer from './loginReducer';
import restrictionsReducer from './restrictionsReducer';
import lifeReducer from './lifeReducer';
import endowmentReducer from './endowmentReducer';
import retirementReducer from './retirementReducer';
import datepickerReducer from './datepickerReducer';
import businessReducer from './businessReducer';

export default combineReducers({
  blog: blogReducer,
  form: reduxFormReducer,
  editForm: editFormReducer,
  nav: navReducer,
  userDetails: userReducer,
  insurance: insuranceReducer,
  tab: tabReducer,
  travel: travelReducer,
  motor: motorReducer,
  loadingScreen: loadingScreenReducer,
  compare: compareReducer,
  pet: petReducer,
  pa: paReducer,
  login: loginReducer,
  restrictions: restrictionsReducer,
  life: lifeReducer,
  endowment: endowmentReducer,
  retirement: retirementReducer,
  datepicker: datepickerReducer,
  business: businessReducer,
});
