import Axios from '../lib/axiosFunctions';

export const SET_GET_QUOTE_NAVBAR = 'SET_GET_QUOTE_NAVBAR';
export const SET_QUOTE_DATA = 'SET_QUOTE_DATA';

export const FORMSTATES = {
  MAIN: { index: 0, path: '' },
  CUSTOMIZE: { index: 1, path: '/customize' },
  REVIEW: { index: 2, path: '/review' },
  CHECKOUT: { index: 3, path: '/checkout' },
  SUBMITTED: { index: 4, path: '/confirmation' },
  // BANKTRANSFER: { index: 5, path: '/bank-transfer' },
};

export const PARTIALFORMSTATES = {
  MAIN: { index: 0, path: '' },
  REVIEW: { index: 2, path: '/review' },
  CHECKOUT: { index: 3, path: '/checkout' },
  SUBMITTED: { index: 4, path: '/confirmation' },
  CUSTOMIZE: { index: 99, path: '/customize' },
  // BANKTRANSFER: { index: 5, path: '/bank-transfer' },
};

export const setGetQuoteNavbar = callback => dispatch => (
  dispatch({
    type: SET_GET_QUOTE_NAVBAR,
    data: callback,
  })
);

export const setQuoteData = data => dispatch => (
  dispatch({
    type: SET_QUOTE_DATA,
    data,
  })
);

export const getForm = async (productID = 1) => {
  try {
    const url = `${process.env.REACT_APP_EB_HOST}/v1/forms/${productID}`;
    const result = await Axios.GET(url);

    // console.log(result);
    if (result.status === 200 && result.data && result.data.page_forms) {
      return result.data.page_forms;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const getSession = async (token) => {
  try {
    const url = `${process.env.REACT_APP_EB_HOST}/v1/sessions/${token}`;
    const result = await Axios.GET(url);

    // console.log(result);
    if (result.status === 200 && result.data) {
      return result.data;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const postSession = async (inputData, productID = 1) => {
  const params = {
    input_data: inputData,
    product_id: productID,
  };

  try {
    const url = `${process.env.REACT_APP_EB_HOST}/v1/sessions`;
    const result = await Axios.POST(url, params);

    // console.log(result);
    if (result.status === 201 && result.data && result.data.token) {
      return result.data.token;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const putSessions = async (inputData, token, status, page, productID = 1, partialFlowStatus = false) => {
  if (!token || !status || !page) {
    return null;
  }

  const params = {
    input_data: inputData,
    product_id: productID,
    token,
    status,
    page,
  };

  try {
    if (partialFlowStatus) {
      return true;
    }
    const url = `${process.env.REACT_APP_EB_HOST}/v1/sessions`;
    const result = await Axios.PUT(url, params);

    // console.log(result);
    return result.status === 200 && result.data && result.data.updated;
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const postQuote = async (inputData, token, productID = 1, promoCode = null) => {
  const promoCodeToUse = promoCode ? { promo_code: promoCode } : {};
  const params = {
    input_data: inputData,
    product_id: productID,
    token,
    ...promoCodeToUse,
  };

  try {
    const url = `${process.env.REACT_APP_EB_HOST}/v1/quote`;
    const result = await Axios.POST(url, params);

    // console.log(result);
    if (result.status === 200 && result.data && result.data.quote) {
      return result.data;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

