export const postBankTransfer = async (params, token) => {
  try {
    if (!params || !token) {
      return null;
    }

    const url = `${process.env.REACT_APP_EB_HOST}/v1/payment/${token}`;
    const result = await Axios.POST(url, params);

    // console.log(result);
    if (result.status === 200 && result.data && result.data.success) {
      return result.data.success;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const verifyOTP = async (token, intent) => {
  try {
    if (!intent || !token) {
      return null;
    }

    const url = `${process.env.REACT_APP_SERVICES_HOST}/v1/eb/payment/charge/${token}/verify?payment_intent=${intent}`;
    const result = await Axios.GET(url);

    // console.log(result);
    if (result.status === 200 && result.data) {
      return result.data;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

export const postComeBackLater = async (params, token) => {
  try {
    if (!params || !token) {
      return null;
    }

    const url = `${process.env.REACT_APP_EB_HOST}/v1/payment/${token}/return`;
    const result = await Axios.POST(url, params);

    // console.log(result);
    if (result.status === 200 && result.data) {
      return result.data;
    }
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};
