import React, { Component } from 'react';
import PropTypes from 'prop-types';

import TabView from '../../components/Templates/TabView/TabView';
import Login from '../../components/Signing/Login';
import NewSignUp from '../../components/Signing/NewSignUp';
import ForgetPassword from '../../components/Signing/ForgetPassword';

import '../../components/Signing/Signing.scss';

class LoginContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      callback: null,
    };

    this.setInitialIndex.bind(this);
  }

  setInitialIndex(index) {
    this.tabView.wrappedInstance.goToIndex(index);
  }

  setSuccessCallback(callback) {
    this.setState({
      callback,
    });
  }

  render() {
    const { callback } = this.state;
    const { closePopUp } = this.props;

    return (
      <div className="login-tab-wrapper">
        <TabView
          ref={node => this.tabView = node}
          maxTabWidthPx={128}
          tabs={[
            'TabLogin-ForgetPassword',
            'TabLogin-Login',
            'TabLogin-NewSignUp',
          ]}
          isDisableDropdown
          hideWidth={99999}
          name="login-tab"
        >
          {[
            <ForgetPassword closePopUp={closePopUp} key="forget-password" />,
            <Login closePopUp={closePopUp} key="login" callback={callback} />,
            <NewSignUp closePopUp={closePopUp} key="new-sign-up" callback={callback} />,
          ]}
        </TabView>
      </div>
    );
  }
}

