import {
  GET_ENDOWMENT_PLANS,
  GET_ENDOWMENT_PLANS_FAILED,
  GET_ENDOWMENT_FIELDS_SUCCESS,
  CLEAR_ENDOWMENT_DATA,
  SET_ENDOWMENT_SELECTED_PLAN_DATA,
  GET_ENDOWMENT_VALID_INSURERS_LIST,
} from '../actions/endowmentActions';

const initialState = {
  plansData: null,
  selectedPlanData: null,
  validInsurersList: null,
  endowmentFields: null,
};

const endowment = (state = initialState, action) => {
  switch (action.type) {
    case GET_ENDOWMENT_FIELDS_SUCCESS:
      return { ...state, endowmentFields: action.data };
    case GET_ENDOWMENT_VALID_INSURERS_LIST:
      return { ...state, validInsurersList: action.data };
    case GET_ENDOWMENT_PLANS:
      return { ...state, plansData: action.data };
    case GET_ENDOWMENT_PLANS_FAILED:
      return { ...state, plansData: ['INVALID'] };
    case CLEAR_ENDOWMENT_DATA:
      return { ...initialState, endowmentFields: state.endowmentFields };
    case SET_ENDOWMENT_SELECTED_PLAN_DATA:
      return { ...state, selectedPlanData: action.data };
    default:
      return state;
  }
};

export default endowment;
