/**
 * compareActions.js
 * Manages the state of the compare view
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
export const SET_PLAN_TYPE = 'SET_TRAVEL_PLAN_TYPE';
export const SET_SELECTED_INSURERS = 'SET_SELECTED_INSURERS';
export const SET_SELECTED_INDEX = 'SET_SELECTED_INDEX';
export const CLEAR_PLAN_TYPE = 'CLEAR_PLAN_TYPE';
export const CLEAR_SELECTED_INSURERS = 'CLEAR_SELECTED_INSURERS';
export const RESET_COMPARE = 'RESET_COMPARE';

export const setPlanType = type => (dispatch) => {
  dispatch({
    type: SET_PLAN_TYPE,
    data: type,
  });
};

export const setSelectedInsurers = insurers => (dispatch) => {
  dispatch({
    type: SET_SELECTED_INSURERS,
    data: insurers,
  });
};

export const setSelectedIndex = index => (dispatch) => {
  dispatch({
    type: SET_SELECTED_INDEX,
    data: index,
  });
};

export const clearPlanType = () => (dispatch) => {
  dispatch({
    type: CLEAR_PLAN_TYPE,
  });
};

export const clearSelectedInsurers = () => (dispatch) => {
  dispatch({
    type: CLEAR_SELECTED_INSURERS,
  });
};

export const resetCompare = () => (dispatch) => {
  dispatch({
    type: RESET_COMPARE,
  });
};
