import {
  SET_GET_QUOTE_NAVBAR,
  SET_QUOTE_DATA,
} from 'actions/businessAction';

const initialState = {
  getQuoteCallback: null,
  quoteData: null,
};

const business = (state = initialState, action) => {
  switch (action.type) {
    case SET_GET_QUOTE_NAVBAR:
      return { ...state, getQuoteCallback: action.data };
    case SET_QUOTE_DATA:
      return { ...state, quoteData: action.data };
    default:
      return state;
  }
};

export default business;
