/**
 * loadingScreenActions.js
 * Actions to handle loading screen
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
export const SET_LOADING = 'SET_LOADING';
export const CLEAR_LOADING = 'CLEAR_LOADING';

export const setLoading = (callBack = null, errorMsg = null, time = null, mainMsg = null) => (dispatch) => {
  dispatch({
    type: SET_LOADING,
    data: {
      callBack,
      errorMsg,
      time,
      mainMsg,
    },
  });
};

export const clearLoading = () => (dispatch) => {
  dispatch({
    type: CLEAR_LOADING,
  });
};
