import axios from 'axios';

const GET = async (url, customHeaders = {}) => {
  let result = null;

  await axios
    .get(url, {
      headers: {
        ...customHeaders,
      },
    })
    .then((response) => {
      result = response;
    })
    .catch((error) => {
      // console.log(url, error);
      result = error.response;
    });

  return result;
};

const POST = async (url, params, customHeaders = {}) => {
  let result = null;

  await axios
    .post(url, params, {
      headers: {
        ...customHeaders,
      },
    })
    .then((response) => {
      result = response;
    })
    .catch((error) => {
      // console.log(url, error);
      result = error.response;
    });

  return result;
};

const PUT = async (url, params, customHeaders = {}) => {
  let result = null;

  await axios
    .put(url, params, {
      headers: {
        ...customHeaders,
      },
    })
    .then((response) => {
      result = response;
    })
    .catch((error) => {
      // console.log(url, error);
      result = error.response;
    });

  return result;
};


export default {
  GET,
  POST,
  PUT,
};

