import { GET_TRAVEL_MAIN_FORM_FIELDS_SUCCESS } from '../actions/travelActions';

const initialState = {
  mainFormFields: null,
};

const travel = (state = initialState, action) => {
  switch (action.type) {
    case GET_TRAVEL_MAIN_FORM_FIELDS_SUCCESS:
      return { ...state, mainFormFields: action.data };
    default:
      return state;
  }
};

export default travel;
