/**
 * loginActions.js
 * Actions for login popup
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
export const SET_LOGIN_POPUP = 'SET_LOGIN_POPUP';

export const setLoginPopup = (popup, loginContainer) => (dispatch) => {
  dispatch({
    type: SET_LOGIN_POPUP,
    data: {
      popup,
      loginContainer,
    },
  });
};
