import {
  GET_RETIREMENT_PLANS,
  GET_RETIREMENT_PLANS_FAILED,
  GET_RETIREMENT_FIELDS_SUCCESS,
  CLEAR_RETIREMENT_DATA,
  SET_RETIREMENT_SELECTED_PLAN_DATA,
  GET_RETIREMENT_VALID_INSURERS_LIST,
} from '../actions/retirementActions';

const initialState = {
  plansData: null,
  selectedPlanData: null,
  validInsurersList: null,
  retirementFields: null,
};

const retirement = (state = initialState, action) => {
  switch (action.type) {
    case GET_RETIREMENT_FIELDS_SUCCESS:
      return { ...state, retirementFields: action.data };
    case GET_RETIREMENT_VALID_INSURERS_LIST:
      return { ...state, validInsurersList: action.data };
    case GET_RETIREMENT_PLANS:
      return { ...state, plansData: action.data };
    case GET_RETIREMENT_PLANS_FAILED:
      return { ...state, plansData: ['INVALID'] };
    case CLEAR_RETIREMENT_DATA:
      return { ...initialState, retirementFields: state.retirementFields };
    case SET_RETIREMENT_SELECTED_PLAN_DATA:
      return { ...state, selectedPlanData: action.data };
    default:
      return state;
  }
};

export default retirement;
