import {
  SET_LOADING,
  CLEAR_LOADING,
} from '../actions/loadingScreenActions';

const initialState = {
  isLoading: false,
  loadingData: null,
};

const loadingScreen = (state = initialState, action) => {
  switch (action.type) {
    case SET_LOADING:
      return { ...state, isLoading: true, loadingData: action.data };
    case CLEAR_LOADING:
      return { ...state, isLoading: false, loadingData: null };
    default:
      return state;
  }
};

export default loadingScreen;
