/**
 * restrictionsActions.js
 * Handles the restriction popup
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
export const SET_RESTRICTION_POPUP = 'SET_RESTRICTION_POPUP';
export const CLEAR_RESTRICTION_POPUP = 'CLEAR_RESTRICTION_POPUP';

export const setRestrictionPopup = data => (dispatch) => {
  dispatch({
    type: SET_RESTRICTION_POPUP,
    data,
  });
};

export const clearRestrictionPopup = () => (dispatch) => {
  dispatch({
    type: CLEAR_RESTRICTION_POPUP,
  });
};
