/**
 * datepickerActions.js
 * Manages Datepicker
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
export const SET_DATEPICKER_INNER = 'SET_DATEPICKER_INNER';

export const setDatepickerInner = datepicker => (dispatch) => {
  dispatch({
    type: SET_DATEPICKER_INNER,
    data: datepicker,
  });
};
