/**
 * tabActions.js
 * Handles tab selection for tab view
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
export const SET_TAB_SELECTION = 'SET_TAB_SELECTION';

export const setTabSelection = (tabName, selection) => (dispatch) => {
  dispatch({
    type: SET_TAB_SELECTION,
    data: {
      tabName,
      selection,
    },
  });
};
