import {
  RESET_INSURANCE_FORM,
  SET_INSURANCE_FORM_STATE,
  SET_HOVERED_INSURER_INDEX,
  GET_CONSTANTS_PASSED,
  FORMSTATES,

  GET_PROMO_SUCCESS,
  GET_PROMO_FAILED,

  NEW_ANONYMOUS_SESSION,
  GET_SESSION_PARAMS_FAILED,
  GET_SESSION_PARAMS_SUCCESS,

  CLEAR_STRIPE,
  GET_STRIPE_SUCCESS,
  GET_STRIPE_FAILED,

  POST_CHECKOUT_SUCCESS,
  POST_CHECKOUT_FAILED,

  GET_PROMO_GIVEAWAY_SUCCESS,
  GET_PROMO_GIVEAWAY_FAILED,

  GET_ALL_PROMO_GIVEAWAY_SUCCESS,
  GET_ALL_PROMO_GIVEAWAY_FAILED,
} from '../actions/insuranceActions';

const initialState = {
  constants: null,
  formState: FORMSTATES.MAIN.index,
  hoveredIndex: null,

  validInsurersList: null,
  sessionId: null,
  checkoutButton: null,
  transactionData: null,
  promoErrorString: null,
  charged: null,

  promoGiveaways: {},
  promoGiveawaysAll: [],
};

const insurance = (state = initialState, action) => {
  switch (action.type) {
    case RESET_INSURANCE_FORM:
      return {
        ...initialState,
        constants: state.constants,
        promoGiveaways: state.promoGiveaways,
      };
    case SET_INSURANCE_FORM_STATE:
      return { ...state, formState: action.data };
    case SET_HOVERED_INSURER_INDEX:
      return {
        ...state,
        hoveredIndex: action.data,
      };
    case GET_CONSTANTS_PASSED:
      return {
        ...state,
        constants: action.data,
      };
    case GET_PROMO_SUCCESS:
      return { ...state, transactionData: action.data, promoErrorString: null };
    case GET_PROMO_FAILED:
      return { ...state, promoErrorString: action.data };
    case NEW_ANONYMOUS_SESSION:
      return {
        ...state,
        sessionId: null,
        checkoutButton: null,
        transactionData: null,
        promoErrorString: null,
        charged: null,
      };
    case GET_SESSION_PARAMS_FAILED:
      return { ...state, sessionId: null };
    case GET_SESSION_PARAMS_SUCCESS:
      return { ...state, sessionId: action.data };
    case CLEAR_STRIPE:
    case GET_STRIPE_FAILED:
      return { ...state, checkoutButton: null };
    case GET_STRIPE_SUCCESS:
      return { ...state, checkoutButton: action.data };
    case POST_CHECKOUT_SUCCESS:
      return { ...state, charged: action.data.charged };
    case POST_CHECKOUT_FAILED:
      return { ...state, charged: false };
    case GET_PROMO_GIVEAWAY_SUCCESS:
      return {
        ...state,
        promoGiveaways: {
          ...state.promoGiveaways,
          [action.data.category]: action.data.value,
        },
      };
    case GET_PROMO_GIVEAWAY_FAILED:
      return {
        ...state,
        promoGiveaways: {
          ...state.promoGiveaways,
          [action.data.category]: null,
        },
      };
    case GET_ALL_PROMO_GIVEAWAY_SUCCESS:
      return {
        ...state,
        promoGiveawaysAll: action.data,
      };
    case GET_ALL_PROMO_GIVEAWAY_FAILED:
      return {
        ...state,
        promoGiveawaysAll: null,
      };
    default:
      return state;
  }
};

export default insurance;
