import _ from 'lodash';
import moment from 'moment';

// Month text for display
const monthTextMapping = [
  'January',
  'Feburary',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

// Parse date symbols from date object
const parseDateSymbols = date => ({
  day: (`0${date.getDate().toString()}`).slice(-2),
  month: (`0${(date.getMonth() + 1).toString()}`).slice(-2),
  year: date.getFullYear().toString(),
});

// Parse date object to slashed string
export const dateToString = (date) => {
  try {
    const parsed = parseDateSymbols(date);
    return `${parsed.day}/${parsed.month}/${parsed.year}`;
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

// Parse date object to store string form
export const dateToStoreString = (date) => {
  try {
    const parsed = parseDateSymbols(date);
    return `${parsed.day} ${monthTextMapping[parsed.month - 1].slice(0, 3)} ${parsed.year}`;
  } catch (error) {
    // eslint-disable-next-line
  }

  return null;
};

// Parse store string to date object
export const storeStringToDate = (storeDate) => {
  // If date is valid
  if (storeDate) {
    // Reform the date string
    const reformParts = storeDate.split(' ');
    // Get date values in string
    const month = _.findIndex(monthTextMapping, item => item.indexOf(reformParts[1]) > -1);
    const monthString = (`0${(month + 1).toString()}`).slice(-2);
    const reformDateString = `${reformParts[0]}/${monthString}/${reformParts[2]}`;

    // If reformed string is valid
    if (moment(reformDateString, 'DD/MM/YYYY', true).isValid()) {
      // Set new date
      return new Date(reformParts[2], month, reformParts[0]);
    }
  } else if (storeDate === null) {
    // Set null if invalid
    return null;
  }

  return -1;
};

// Parse ISO date string to form date string
export const ISOStringToFormDate = (isoString) => {
  // The ISO Date
  const date = new Date(isoString);

  return dateToStoreString(date);
};

// Parse form date to ISO date string
export const formDateToISOString = (formDate) => {
  if (formDate) {
    const date = storeStringToDate(formDate);
    return date.toISOString();
  }

  return null;
};

// Parse store string to slashed date string
export const storeStringToDateString = (formDate) => {
  const date = storeStringToDate(formDate);
  if (date && date.constructor === Date) {
    return dateToString(date);
  }

  return null;
};

// Parse slashed date to date object
export const slashedStringDateToDate = (slashedString) => {
  const dateArray = slashedString.split('/');

  try {
    return new Date(dateArray[2], dateArray[1] - 1, dateArray[0]);
  } catch (error) {
    return null;
  }
};

// Get age from date object
export const getAge = (birthDate) => {
  const today = new Date();
  let age = today.getFullYear() - birthDate.getFullYear();
  const m = today.getMonth() - birthDate.getMonth();
  if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
    age -= 1;
  }
  return age;
};

// Get age from date object
export const isDateEqual = (date1, date2) => {
  try {
    return date1 && date2 && date1.getTime() === date2.getTime();
  } catch (error) {
    return false;
  }
};

// Calculate days in between dates
export const daysDiff = (date1, date2) => {
  const timeDiff = Math.abs(date2.getTime() - date1.getTime());
  return Math.ceil(timeDiff / (1000 * 3600 * 24)) + 1;
};

export const getRangeString = (startDate, endDate) => {
  try {
    return `${startDate.getDate()} ${monthTextMapping[startDate.getMonth()].slice(0, 3)} - ${endDate.getDate()} ${monthTextMapping[endDate.getMonth()].slice(0, 3)}`;
  } catch (error) {
    return false;
  }
};
