import { storeStringToDateString } from '../lib/dateFunctions';
import Axios from '../lib/axiosFunctions';

export const GET_PA_PLANS = 'GET_PET_PLANS';
export const GET_PA_PLANS_FAILED = 'GET_PA_PLANS_FAILED';
export const CLEAR_PA_DATA = 'CLEAR_PET_DATA';

export const GET_PA_FIELDS_SUCCESS = 'GET_PA_FIELDS_SUCCESS';
export const GET_PA_FIELDS_FAILED = 'GET_PA_FIELDS_FAILED';

export const GET_PA_VALID_INSURERS_LIST = 'GET_PA_VALID_INSURERS_LIST';
export const GET_PA_VALID_INSURERS_LIST_FAILED = 'GET_PA_VALID_INSURERS_LIST_FAILED';

export const SET_PA_SELECTED_PLAN_DATA = 'SET_PET_SELECTED_PLAN_DATA';

export const parsePAForm = (paForm, insurers = null) => {
  const result = {
    plan_type: paForm.plan_type,
    my_occupation: paForm.my_occupation,
    start_date: storeStringToDateString(paForm.start_date),
    insurers,
  };

  if (paForm.plan_type === 'Me & My Spouse') {
    result.spouse_occupation = paForm.spouse_occupation;
  }

  if (paForm.plan_type === 'Me & My Family') {
    result.family_members = paForm.family_members;
    result.num_children = paForm.num_children;

    if (paForm.family_members === '2 Adults') {
      result.spouse_occupation = paForm.spouse_occupation;
    }
  }

  return result;
};

export const getPAFields = () => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/2/formfields`;
  const result = await Axios.GET(url);

  if (
    !result ||
    result.status !== 200
  ) {
    return dispatch({
      type: GET_PA_FIELDS_FAILED,
      data: 'PA Fields data invalid',
    });
  }

  // console.log(result.data);

  return dispatch({
    type: GET_PA_FIELDS_SUCCESS,
    data: result.data,
  });
};

export const getPAPlans = params => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/2/compare`;
  const result = await Axios.POST(url, params);

  // console.log(result);
  if (
    !result ||
    result.status !== 201
  ) {
    return dispatch({
      type: GET_PA_PLANS_FAILED,
      data: 'Get PA plans failed',
    });
  }

  return dispatch({
    type: GET_PA_PLANS,
    data: result.data.insurances,
  });
};

export const getValidInsurersList = params => async (dispatch) => {
  const url = `${process.env.REACT_APP_API_HOST}/v2/categories/2/insurers`;
  const paramsToUse = { ...params };
  if (paramsToUse.num_children) {
    paramsToUse.num_children = paramsToUse.num_children.toString();
  }

  const result = await Axios.POST(url, paramsToUse);

  // console.log(result);
  if (
    !result ||
    result.status !== 201
  ) {
    return dispatch({
      type: GET_PA_VALID_INSURERS_LIST_FAILED,
      data: 'Get PA insurer list failed',
    });
  }

  return dispatch({
    type: GET_PA_VALID_INSURERS_LIST,
    data: result.data.insurances,
  });
};

export const clearPAData = () => (dispatch) => {
  dispatch({
    type: CLEAR_PA_DATA,
  });
};

export const setSelectedPlanData = data => (dispatch) => {
  dispatch({
    type: SET_PA_SELECTED_PLAN_DATA,
    data,
  });
};

export const getSessionParams = (paForm, paDetailsForm, selectedPlanData) => {
  try {
    const sessionParams = {
      plan_type: paForm.plan_type,
      occupation: paForm.my_occupation,
      industry: paDetailsForm.industry,
      start_date: storeStringToDateString(paForm.start_date),
      policy_type: selectedPlanData.planType,
      policy_name: selectedPlanData.info.product_name,
      email: paDetailsForm.email,
      full_name: paDetailsForm.name,
      nationality: paDetailsForm.nationality,
      id_type: paDetailsForm.id_type,
      nric: paDetailsForm.nric,
      race: paDetailsForm.race,
      gender: paDetailsForm.gender,
      birth_date: storeStringToDateString(paDetailsForm.birth_date),
      marital_status: paDetailsForm.marital_status,
      postal: paDetailsForm.postal,
      mobile_number: paDetailsForm.mobile_number,
      category: 'Personal Accident',
    };

    if (paForm.plan_type !== 'Myself') {
      let numAdult = '1';
      const isSpouse = paForm.plan_type === 'Me & My Spouse';
      const isFamily = paForm.plan_type === 'Me & My Family';

      if (
        isSpouse ||
        (isFamily && paForm.family_members === '2 Adults')
      ) {
        numAdult = '2';
      }
      sessionParams.num_adult = numAdult;
      sessionParams.num_children = isFamily ? paForm.num_children.toString() : '0';

      let spouse = [];
      if (numAdult === '2') {
        spouse = [{
          full_name: paDetailsForm['spouse-name'],
          nationality: paDetailsForm['spouse-nationality'],
          id_type: paDetailsForm['spouse-id_type'],
          nric: paDetailsForm['spouse-nric'],
          birth_date: storeStringToDateString(paDetailsForm['spouse-birth_date']),
          relationship: 'Spouse',
          gender: paDetailsForm['spouse-gender'],
          occupation: paDetailsForm['spouse-occupation'],
          industry: paDetailsForm['spouse-industry'],
        }];
      }

      const children = [];
      if (isFamily) {
        for (let i = 0; i < paForm.num_children; ++i) {
          const prefix = `fam-child_${i + 1}-`;

          children.push({
            full_name: paDetailsForm[`${prefix}name`],
            nationality: paDetailsForm[`${prefix}nationality`],
            id_type: paDetailsForm[`${prefix}id_type`],
            nric: paDetailsForm[`${prefix}nric`],
            birth_date: storeStringToDateString(paDetailsForm[`${prefix}birth_date`]),
            relationship: 'Children',
            gender: paDetailsForm[`${prefix}gender`],
            full_school_education: paDetailsForm[`${prefix}education`],
          });
        }
      }

      sessionParams.family = [...spouse, ...children];
    }

    return {
      client_display_name: selectedPlanData.info.insurer_name,
      client_name: selectedPlanData.info.insurer_slug,
      policy_name: selectedPlanData.info.product_name,
      email: paDetailsForm.email,
      session_params: sessionParams,
      start_date: storeStringToDateString(paForm.start_date),
    };
  } catch (error) {
    return null;
  }
};
