import {
  GET_LIFE_PLANS,
  GET_LIFE_PLANS_FAILED,
  GET_LIFE_FIELDS_SUCCESS,
  CLEAR_LIFE_DATA,
  SET_LIFE_SELECTED_PLAN_DATA,
  GET_LIFE_VALID_INSURERS_LIST,
} from '../actions/lifeActions';

const initialState = {
  plansData: null,
  selectedPlanData: null,
  validInsurersList: null,
  lifeFields: null,
};

const life = (state = initialState, action) => {
  switch (action.type) {
    case GET_LIFE_FIELDS_SUCCESS:
      return { ...state, lifeFields: action.data };
    case GET_LIFE_VALID_INSURERS_LIST:
      return { ...state, validInsurersList: action.data };
    case GET_LIFE_PLANS:
      return { ...state, plansData: action.data };
    case GET_LIFE_PLANS_FAILED:
      return { ...state, plansData: ['INVALID'] };
    case CLEAR_LIFE_DATA:
      return { ...initialState, lifeFields: state.lifeFields };
    case SET_LIFE_SELECTED_PLAN_DATA:
      return { ...state, selectedPlanData: action.data };
    default:
      return state;
  }
};

export default life;
